import { CheckOutlined } from '@ant-design/icons'
import React, { useEffect, useRef, useState } from 'react'
import clsx from 'clsx'
import { tutorialType } from '../containers/TypeProps/TutorialProps'
import useClickOutItem from '../hooks/useClickOutItem'

interface Props {
    isList?: boolean
    onClickItem?: (value: any) => void
    setItemDelete?: (value: any) => void
    onDoubleClick?: (value: any) => void
    itemDelete?: number[]
    value: number
    ListTutorial?: tutorialType
    roles?: string
}

function UserManualItem(props: Props) {
    const { isList, roles, setItemDelete, itemDelete, onDoubleClick, onClickItem, ListTutorial, value } = props

    const { isShow, setShow, ref } = useClickOutItem(false)
    // const videoRef = useRef<HTMLVideoElement>(null)

    useEffect(() => {
        let timeout: any
        if (roles !== 'STUDENT') {
            if (!isShow && isList) {
                timeout = setTimeout(() => {
                    if (value === itemDelete![0]) {
                        setItemDelete!([])
                    }
                }, 250)
            }
        }
        return () => {
            clearTimeout(timeout)
        }
    }, [isShow, isList, itemDelete, setItemDelete])

    const handleActive = () => {
        if (isList && onClickItem) {
            setShow!(true)
            onClickItem!(value)
        }
    }

    const deleteItems = () => {
        if (itemDelete!.includes(value)) {
            setItemDelete!([])
        } else {
            setItemDelete!([value])
        }
    }
    return (
        <div
            ref={ref}
            onClick={handleActive}
            onDoubleClick={() => onDoubleClick!(ListTutorial.tutorial_video)}
            className={clsx('grid-flow-row grid  rounded-lg border-2  border-solid', {
                'border-light_blue': isShow,
                'border-slate-300': !isShow,
                'grid-flow-col grid-cols-12 h-[80px] gap-x-2': !isList,
                'grid-rows-5 h-[212px]': isList,
            })}
        >
            <div
                className={clsx('bg-slate-300 overflow-hidden relative h-auto', {
                    'row-span-3 ': isList,
                    'col-span-2': !isList,
                })}
            >
                {' '}
                <div className="absolute top-[50%] left-[50%] translate-y-[-50%] translate-x-[-50%] ">
                    <img
                        src={ListTutorial?.tutorial_image ? ListTutorial?.tutorial_image : 'imgplayorpause.png'}
                        alt={ListTutorial?.tutorial_title || ''}
                    />
                </div>
                {/* <video autoPlay={false} ref={videoRef}>
                    <source
                        src={
                            // ListTutorial?.tutorial_video
                            //     ? ListTutorial?.tutorial_video
                            'http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerEscapes.mp4'
                        }
                    ></source>
                </video> */}
                {/* <div className="absolute flex items-center rounded-tl-lg justify-center bottom-0 right-0 w-[43px] font-medium   bg-[#999999] text-[#000]">
                    {Number(+videoRef?.current?.duration) ? Number(+videoRef?.current?.duration.toFixed(2)) : null}
                </div> */}
            </div>
            <div className={clsx({ 'row-span-2 p-2': isList, 'col-span-9': !isList })}>
                <p className="text-xs font-bold">{ListTutorial?.tutorial_title}</p>
            </div>
            {roles !== 'STUDENT' ? (
                <div
                    className={clsx('flex justify-center items-center', {
                        'col-span-1': !isList,
                        display_none: isList,
                    })}
                >
                    <input
                        type="checkbox"
                        onChange={deleteItems}
                        checked={itemDelete!.includes(value)}
                        name="user_item"
                        hidden
                        id={`userItem_${value}`}
                    />
                    <label htmlFor={`userItem_${value}`} className="checkbox-item">
                        <CheckOutlined />
                    </label>
                </div>
            ) : (
                ''
            )}
        </div>
    )
}

export default UserManualItem
