import React from 'react'

type props = {
    title?: string
    subtitleEng?: string
}
const Title = ({ title, subtitleEng }: props) => {
    return (
        <div>
            <h1 className="text-center md:text-3xl text-[#373737] text-xl font-semibold ">{title}</h1>
            {subtitleEng && <p className="text-center sm:text-2xml text-[#373737] text-[1.2rem] font-normal my-5">{subtitleEng}</p>}

            <div className="home__body-item-line flex justify-center mt-3 m-auto">
                <div></div>
                <div></div>
                <div></div>
            </div>
        </div>
    )
}

export default Title
