import React, { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Quiz, { QuizType } from '../../components/Quiz'
import { ContextModal } from '../../Context/ModalContext'
import { CourseService } from '../../services/CourseService'
import { ExamService } from '../../services/ExamService'
import { __Date } from '../../types'
import DialogActionSuccess from '../common/DialogActionSuccess'
import DialogWarningConfirm from '../common/DialogWarningConfirm'

type Props = {}
export const DATA_LIMIT = 5

const QuizContainer = (props: Props) => {
    const { setModal, showModal } = useContext(ContextModal)

    const naviagte = useNavigate()
    const params = useParams()
    const { courseId, quizId } = params

    const [courseData, setCourseData] = useState(null)
    const [examData, setExamData] = useState(null)

    const getCourseData = () => {
        CourseService.getCourseById(courseId)
            .then((data) => setCourseData(data))
            .catch((err) => console.log(err))
    }

    const getExamData = () => {
        ExamService.getExamById(quizId)
            .then((data) => {
                setExamData(data)
                setQuizes([...data.questions])
            })
            .catch((err) => console.log(err))
    }

    useEffect(() => {
        getCourseData()
        getExamData()
    }, [])

    const [quizes, setQuizes] = useState<QuizType[]>([])
    const [pages] = useState(Math.round(quizes.length / DATA_LIMIT))
    const [currentPage, setCurrentPage] = useState(1)

    const handleChooseAnswer = (quizId: number, answerIdx: number) => {
        let tempArr = quizes
        const quizIdx = tempArr.findIndex((item) => item.id == quizId)

        for (let index in tempArr[quizIdx].answers) {
            tempArr[quizIdx].answers[index] = {
                ...tempArr[quizIdx].answers[index],
                answersStatus: index == answerIdx.toString() ? '1' : '0',
            }
        }
        setQuizes([...tempArr])
    }

    function goToPreviousPage() {
        if (currentPage > 1) {
            setCurrentPage((page) => page - 1)
        }
    }
    function goToNextPage() {
        if (currentPage < pages) {
            setCurrentPage((page) => page + 1)
        }
    }

    const hasChosenAnswer = (item: QuizType) => {
        let hasChosen = false
        for (let answer of item.answers) {
            if (answer.answersStatus == '1') {
                hasChosen = true
                break
            }
        }
        return hasChosen
    }

    function changePage(id: number, questionIndex: number) {
        const pageNumber = Math.ceil(questionIndex / DATA_LIMIT)
        setCurrentPage(pageNumber)
        document.getElementById(`${id}`).scrollIntoView({ behavior: 'smooth' })
    }
    const getPaginatedData = () => {
        const startIndex = currentPage * DATA_LIMIT - DATA_LIMIT
        const endIndex = startIndex + DATA_LIMIT
        return [...quizes.slice(startIndex, endIndex)]
    }

    const handleSubmitModal = () => {
        showModal(true)
        setModal(<DialogWarningConfirm title="Xác nhận nộp bài" onClick={handleSubmit} />)
    }

    const handleSubmit = () => {
        const data = {
            id: quizId,
            questions: quizes,
            typeOfExams: {
                id: 1,
            },
        }
        ExamService.postExamByStudent(data)
            .then(() => {
                console.log('Success')
                // naviagte('/')
            })
            .catch((err) => console.log(err))
    }

    return (
        <>
            <div className="flex-row flex grow">
                <div className="quiz flex-1 flex-col p-[23px]">
                    <div className="border-solid border-2 border-gray-300 rounded-lg p-[20px] mt-[23px]">
                        <h1 className="uppercase font-[700] text-[40px] text-primary_blue leading-[48px] will-change-contents">
                            {courseData?.courseName}
                        </h1>
                        <p className="text-[#636363] font-[500] text-[14px] leading-[17px] mt-[32px]">
                            <a className="text-inherit hover:underline" href="/teacher-central">
                                Trung tâm kiểm soát
                            </a>{' '}
                            / <a className="text-inherit hover:underline">Khóa học của tôi</a> /{' '}
                            <a className="text-inherit hover:underline" href="/">
                                {courseData?.courseName}
                            </a>
                        </p>
                    </div>
                    <div className="text-primary_blue text-4xl pt-8 font-medium rounded-lg border-solid border-2 p-8 py-12 mt-12 mb-5 border-gray-300 ">
                        {examData?.examName}
                    </div>
                    {/* quiz */}

                    <div className="rounded-lg border-solid border-2 px-6 py-6 mb-[23px] border-gray-300 text-lg text-[#636363] font-bold">
                        {/* quizes */}
                        <Quiz quizes={getPaginatedData()} changeAnswer={handleChooseAnswer} currentPage={currentPage} />

                        <div className="flex justify-end mt-10 mb-2">
                            <button
                                className={`rounded-lg border-solid border-2 my-2 h-12 px-4 m-2 border-gray-300 p-2 bg-[#6363631a] 
                ${currentPage === 1 ? 'disabled cursor-not-allowed opacity-70' : ''}`}
                                onClick={goToPreviousPage}
                            >
                                Quay lại
                            </button>
                            <button
                                className={`rounded-lg border-solid border-2 my-2 h-12 px-4 p-2 m-2 text-[#fff] bg-[#636363]  
                ${currentPage === pages ? 'disabled cursor-not-allowed opacity-70' : ''}`}
                                onClick={goToNextPage}
                            >
                                Tiếp theo
                            </button>
                        </div>
                    </div>

                    <div className="flex flex-row-reverse mb-[40px]">
                        <button
                            className={`rounded-lg border-solid border-2 px-4 p-2 text-[#fff] bg-[#636363]`}
                            onClick={() => handleSubmitModal()}
                        >
                            Nộp bài
                        </button>
                    </div>
                </div>

                <div className="lg:w-[444px] bg-[#FBFBFB] border-l-2 p-6 border-solid border-[#D4D4D4]">
                    <div className="sticky top-[23px] w-[400px] right-[21px] border-2 border-solid border-[#D4D4D4] rounded-lg">
                        <div className="text-2xl bold bg-[#F2F2F2] p-2 rounded-t-lg text-[#636363] text-center">
                            Điều hướng câu hỏi
                        </div>
                        <div className="flex flex-row flex-wrap pl-6 pt-2 pb-12">
                            {quizes.map((item, index) => (
                                <button
                                    key={item.id}
                                    id={`${item.id}`}
                                    onClick={() => changePage(item.id, index + 1)}
                                    className={`${
                                        Math.ceil((index + 1) / DATA_LIMIT) == currentPage ? 'border-[#737373]' : ''
                                    } ${hasChosenAnswer(item) ? 'border-b-[#909090] border-b-[16px]' : ''}
                  w-[15%] pt-2 pb-8 mt-4 mr-4 rounded border-solid border-2 border-[#D4D4D4] hover:bg-[#f2f2f2] active:bg-[#F2F2F2]`}
                                >
                                    {index + 1}
                                </button>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default QuizContainer
