import React, { ChangeEvent, useContext, useEffect, useRef, useState } from 'react'
import { useNavigate, useParams, useSearchParams } from 'react-router-dom'
import Paging from '../components/Paging'
import { ContextLayout } from '../layout/Layout'
import { NewsService } from '../services/NewsService'
import { SearchAllService } from '../services/SearchAllService'
import { CourseSearch, NewsSearch } from '../types'

function SearchPage() {
    const [searchParams] = useSearchParams()
    const navigate = useNavigate()
    const [query, setQuery] = useState<string | null>(null)
    const naviPath = useRef(null)
    const [currenPage, setCurrentPage] = useState<any>(1)
    const [totalPage, setTotalPages] = useState<number | null>(1)
    const [newsSearch, setNewsSearch] = useState<NewsSearch[]>([])
    const [totalElementSearch, setTotalElementSearch] = useState<number | null>(null)
    const { AttachChidrenSideRight } = useContext(ContextLayout)

    const handleSearch = (event: ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value
        setQuery(value)
        if (value == '') {
            setNewsSearch([])
        }
        value === '' ? searchParams.set('search', ' ') : searchParams.set('search', value)
    }
    const viewPageSearch = (event: React.MouseEvent<HTMLDivElement>, id: number) => {
        navigate(`/home-new/view/${id}`)
    }
    const getSeach = async (page?: number, size?: number) => {
        const result = await NewsService.searchNews(query, { page, size })
        if (result) {
            const { totalPages, content, pageable, totalElements, size } = result
            setTotalElementSearch(totalElements)
            setNewsSearch(content)
            setTotalPages(totalPages)
        }
    }
    useEffect(() => {
        AttachChidrenSideRight(null)
        const queryString = searchParams.get('search')
        setQuery(queryString)
        if (query && query != '') {
            if (naviPath.current) {
                clearTimeout(naviPath.current)
            }
            naviPath.current = setTimeout(() => {
                navigate(`?search=${query}`)
                getSeach(0, 10)
            }, 500)
        } else {
            clearTimeout(naviPath.current)
            // navigate('')
        }
    }, [query])
    return (
        <div className="w-full min-h-screen pb-14 flex flex-col overflow-hidden">
            <img className="w-full" src="/img/Search-01.png" />
            <div className="w-[70%] h-auto bg-white mt-[-400px] mx-auto mb-14 p-9 rounded-[10px] border-solid border border-[#49BD59]">
                <div className="flex items-center justify-between">
                    <div className="xl:w-4/5 w-[75%] relative">
                        <img
                            className="absolute left-5 top-[50%] -translate-y-[50%] w-6"
                            src="/img/searchIn.png"
                            alt="tìm kiếm"
                        />
                        <input
                            value={query ? query : ''}
                            onChange={(event) => handleSearch(event)}
                            className="w-full h-full pr-5 pl-14 text-2xl border border-solid rounded-[10px] py-3 focus:outline  focus:divide-solid outline-blue-500 focus:outline-1"
                            type="text"
                            placeholder="tìm kiếm"
                        />
                    </div>
                    <button className="w-auto px-5 flex justify-between items-center py-3 text-slate-50 bg-light_blue font-bold text-2xl border border-solid rounded-[10px]">
                        <img className="w-6 mr-3" src="/img/searchButton.png" alt="button search" />
                        Tìm kiếm
                    </button>
                </div>
                {newsSearch.length > 0 && query != '' && (
                    <div>
                        <p className="my-5 font-bold text-lg">
                            Kết quả tìm kiếm: <b className="text-[#242424]">{query}</b> ({totalElementSearch} kết quả)
                        </p>
                        <div>
                            {newsSearch.map((newItems, index) => {
                                return (
                                    <div
                                        onClick={(event) => viewPageSearch(event, newItems.id)}
                                        className="w-full cursor-pointer grid grid-cols-5 rounded-[10px] border-solid border border-[#49BD59] mb-3 "
                                    >
                                        <div className="col-span-1">
                                            <img className="w-full h-full" src="/img/imageNew.png" alt="ảnh" />
                                        </div>
                                        <div className="col-span-4 text-lg px-7">
                                            <p className="font-bold mb-3 text-[#242424]">{newItems.news_title}</p>
                                            <p className="mb-3">Danh mục: News</p>
                                            <p className="mb-3">Mô tả: {newItems.news_description}</p>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </div>
                )}
                {/* {newsSearch.length > 0 && ( */}
                <Paging currenPage={1} setCurrentPage={setCurrentPage} total={2} />
                {/* )} */}
            </div>
        </div>
    )
}

export default SearchPage
