import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { NewsService } from '../../services/NewsService'
import { fomatDate } from '../fomartDate'

type stateType = {
    id: number
    department_image: string
    department_name: string
    department_type: string
    courses: []
}
type stateNews = {
    id: number
    news_isDisplay: string
    news_created_date: string
    news_title: string
    news_description: string
    news_content: string
    news_image: string | null
}[]

function HomeNew() {
    const [listNew, setListNew] = useState<stateNews>([])
    const [currenPage, setCurrentPage] = useState<any | null>(0)
    const [totalPage, setTotalPage] = useState<number | null>(1)
    const navigate = useNavigate()
    const getNew = async (page?: number, size?: number) => {
        const result = await NewsService.getNewsBySortDate(page, size)
        if (result) {
            const { totalPages, content, pageable, totalElements, size } = result
            setListNew([...listNew, ...content])
            setTotalPage(totalPages)
        }
    }
    const handleClickView = (id: number) => {
        navigate(`view/${id}`)
    }
    const handleSetCurrentPage = () => {
        setCurrentPage((prevState) => prevState + 1)
    }
    useEffect(() => {
        if (!currenPage) {
            window.scrollTo({
                top: 0,
                left: 0,
            })
        }
        getNew(currenPage, 16)
    }, [currenPage])

    return (
        <>
            <div className="container-or m-auto w-full">
                <div className="home__body-item mt-20 w-[68.90625%] m-auto">
                    <h1 className="text-center text-3xl font-bold mt-[93px]">DANH SÁCH TIN TỨC TỔNG HỢP SDU - LMS</h1>
                    <div className="home__body-item-line flex justify-center mt-3 m-auto">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>

                    <div className="grid sx:grid-cols-1 sx:gap-y-10 md:grid-cols-2 my-[100px] gap-7">
                        {listNew.length > 0 &&
                            listNew.map((news, index) => {
                                return (
                                    <div
                                        key={index}
                                        className="grid gap-3 sx:grid-cols-1 sm:grid-cols-4  grid-cols-1 h-auto grid-flow-row"
                                    >
                                        <div className="flex sx:col-span-2 max-h-[120px] min-h-[120px] sm:col-span-1 justify-center items-center bg-slate-300 min-w-[162px] max-w-[162px]">
                                            <img
                                                className="w-ful h-full"
                                                src={news.news_image ? news.news_image : 'imgplayorpause.png'}
                                            />
                                        </div>
                                        <div
                                            onClick={() => handleClickView(news.id)}
                                            className="col-span-3 rounded-md p-1 cursor-pointer flex flex-col justify-between pl-[20px]"
                                        >
                                            <p className="text-2xl font-medium text-[#000000] mb-5">
                                                {news.news_title}
                                            </p>
                                            <p className="text-slate-400">{fomatDate(news.news_created_date)}</p>
                                        </div>
                                    </div>
                                )
                            })}
                    </div>
                    <div className="flex justify-center mt-14">
                        <button
                            onClick={handleSetCurrentPage}
                            className="btn-see_more rounded-md font-semibold bg-[#FCFCFC]"
                        >
                            <span className="mr-3" style={{ color: 'blue_bg' }}>
                                Xem thêm
                            </span>
                            <img src="\img\icon_arrowright_red.png" alt="" />
                        </button>
                    </div>
                </div>

                <div className="flex justify-center mt-20">
                    <div className="home__body-item-line flex justify-center mt-3 m-auto mb-7">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default HomeNew
