import { useContext, useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import UserService from '../../services/UserService'
import BannerHome from '../../components/BannerHome'
import { DepartmentService } from '../../services/DepartmentService'
import { NewsService } from '../../services/NewsService'
import { UserManualService } from '../../services/UserManualService'
import { fomatDate } from '../fomartDate'
import { tutorialType } from '../TypeProps/TutorialProps'
import { ADMIN, STUDENT } from '../../types'
import ViewVideoComponent from '../../components/ViewVideoComponent'
import { ContextMessage } from './../../Context/ShowMessage'

type stateType = {
    id: number
    department_image: string
    department_name: string
    department_type: string
    courses: []
}
type stateNews = {
    id: number
    news_isDisplay: string
    news_created_date: string
    news_title: string
    news_description: string
    news_content: string
    news_image: string | null
}[]

function HomeContainer() {
    const [departmentSimple, setDepartmentSimple] = useState<stateType[]>([])
    const [listNew, setListNew] = useState<stateNews>([])
    const [ListTutorials, setListTutorials] = useState<tutorialType[]>([])
    const [isShowVideo, setShowVideo] = useState<boolean>(false)
    const [urlVider, setUrlVideo] = useState<string | null>(null)
    const { pushMessage } = useContext(ContextMessage)

    const Navigate = useNavigate()
    // xử lý login và logout

    const viewVideo = (videoUrl: string) => {
        if (videoUrl) {
            setUrlVideo(videoUrl)
        }
        setShowVideo(true)
    }
    const getNew = async () => {
        const result = await NewsService.getFeaturedNews()
        if (result) {
            setListNew(result)
        }
    }

    const handleViewFeature = (id: number) => {
        Navigate(`home-new/view/${id}`)
    }
    const handleHefp = () => {
        const isadmin = UserService.hasRole([ADMIN])
        if (UserService.isLoggedIn()) {
            if (isadmin) {
                Navigate('user-manual')
            } else {
                Navigate('lms-help')
            }
        } else {
            pushMessage({
                title: 'Cảnh báo',
                type: 'WARN',
                message: 'Vui lòng đăng nhập',
            })
        }
    }
    // call api tutorials
    const callTutorial = async () => {
        const result = await UserManualService.getTutorialisDisplay()
        if (result) {
            setListTutorials(result)
        }
        return result
    }
    const getFeatureDepartments = async () => {
        const result = await DepartmentService.getFeatureDepartments()
        if (result) {
            const { totalPages, content, pageable, totalElements, size } = result
            setDepartmentSimple(content)
        }
    }

    useEffect(() => {
        getFeatureDepartments()
        getNew()
        callTutorial()
    }, [])

    return (
        <>
            <div>
                <BannerHome />
            </div>
            {/* set h-screen lam container chi an height = 100vw */}
            <div className="container-or m-auto w-full ">
                <div className="home__body-item mt-20">
                    <h1 className="text-center text-3xl font-semibold text-[#373737]">TIN TỨC MỚI VỀ SDU LMS</h1>
                    <div className="home__body-item-line flex justify-center mt-3 m-auto">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>

                    <div className="grid md:grid-cols-3  grid-cols-1 mt-12 md:gap-4 gap-y-2 w-[69.2708333%] m-auto">
                        <div className="col-span-2 max-h-[750px] flex flex-col">
                            <div className="home__list-news bg-slate-300 min-h-[455px] max-h-[455px] rounded border-2 border-solid border-light_blue">
                                <img
                                    onClick={listNew.length > 0 ? () => handleViewFeature(listNew[0].id) : undefined}
                                    className="w-full max-h-[455px] object-cover rounded-md"
                                    src={listNew[0]?.news_image}
                                    //     listNew === null
                                    //         ? ' https://baothainguyen.vn/UserFiles/image/img9319.jpg'
                                    //         : listNew[1]?.news_image
                                    // }
                                    alt={listNew[0]?.news_title ? listNew[0].news_title : ''}
                                />
                                <div className="home__sub-news">
                                    <p className="text-2xl font-semibold text-white">
                                        {listNew.length > 0 && listNew[0]?.news_title}
                                    </p>
                                    <p className="text-white text-base">
                                        {listNew.length > 0 && fomatDate(listNew[0]?.news_created_date)}
                                    </p>
                                </div>
                            </div>
                            <div className="grid grid-cols-2 gap-4 mt-4">
                                <div className="rounded-md min-h-[267.8px] max-h-[267.8px] border-2 border-solid border-light_blue bg-slate-300">
                                    {listNew.length > 0 && (
                                        <div className="home__list-news h-full">
                                            <img
                                                onClick={listNew.length > 0 && (() => handleViewFeature(listNew[1].id))}
                                                className="w-full rounded-md object-cover h-full"
                                                src={listNew[1]?.news_image ? listNew[1]?.news_image : ''}
                                                alt={listNew[1]?.news_title ? listNew[1].news_title : ''}
                                            />

                                            <div className="home__sub-news bottom-2">
                                                <p className="text-base font-semibold text-white">
                                                    {listNew[1]?.news_title}
                                                </p>
                                                <p className="text-white text-base text-current">
                                                    {fomatDate(listNew[1]?.news_created_date)}
                                                </p>
                                            </div>
                                        </div>
                                    )}
                                </div>

                                <div className="rounded-md min-h-[267.8px] max-h-[267.8px] border-2 border-solid border-light_blue bg-slate-300">
                                    {listNew.length > 0 && (
                                        <div className="home__list-news h-full">
                                            <img
                                                onClick={listNew.length > 0 && (() => handleViewFeature(listNew[2].id))}
                                                className="w-full object-cover rounded-md h-full"
                                                src={listNew[2]?.news_image ? listNew[2]?.news_image : ''}
                                                alt={listNew[2]?.news_title ? listNew[2].news_title : ''}
                                            />

                                            <div className="home__sub-news bottom-2">
                                                <p className="text-base font-semibold text-white">
                                                    {listNew.length > 0 && listNew[2]?.news_title}
                                                </p>
                                                <p className="text-white text-base text-current">
                                                    {listNew.length > 0 && fomatDate(listNew[2]?.news_created_date)}
                                                </p>
                                            </div>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </div>
                        {/* <div className="grid gap-4 sm:grid-cols-3 md:grid-cols-1">
                            <div className="row-span-1 min-h-[50px] md:row-span-2 rounded-md border-2 border-solid border-light_blue bg-slate-300"> */}
                        <div className="grid gap-4  sm:grid-cols-3 md:grid-cols-1 w-full">
                            <div className="row-span-1 min-h-[50px] row-span-2 md:row-span-2 rounded-md border-2 border-solid border-light_blue bg-slate-300">
                                {listNew.length > 0 && (
                                    <div className="relative min-h-[267.8px] max-h-[267.8px]">
                                        <img
                                            onClick={listNew.length > 0 && (() => handleViewFeature(listNew[3].id))}
                                            className="w-full overflow-hidden object-cover rounded-md h-full"
                                            src={listNew[3]?.news_image ? listNew[3]?.news_image : ''}
                                            alt={listNew[3]?.news_title ? listNew[3].news_title : ''}
                                        />

                                        <div className="home__sub-news bottom-2">
                                            <p className="text-base font-semibold text-white">
                                                {listNew[3]?.news_title}
                                            </p>
                                            <p className="text-white text-base text-current">
                                                {fomatDate(listNew[3]?.news_created_date)}
                                            </p>
                                        </div>
                                    </div>
                                )}
                            </div>

                            <div className="row-span-2 md:row-span-2 rounded-md border-[0.5px] border-solid border-light_blue bg-slate-300">
                                {listNew.length > 0 && (
                                    <div className="relative overflow-hidden min-h-[267.8px] max-h-[267.8px] h-full">
                                        <img
                                            onClick={listNew.length > 0 && (() => handleViewFeature(listNew[4].id))}
                                            className="w-full object-cover  rounded-md h-full"
                                            src={listNew[4]?.news_image ? listNew[4]?.news_image : ''}
                                            alt={listNew[4]?.news_title ? listNew[4].news_title : ''}
                                        />

                                        <div className="home__sub-news bottom-2">
                                            <p className="text-base font-semibold text-white">
                                                {listNew.length > 0 && listNew[4]?.news_title}
                                            </p>
                                            <p className="text-white text-base text-current">
                                                {listNew.length > 0 && fomatDate(listNew[4]?.news_created_date)}
                                            </p>
                                        </div>
                                    </div>
                                )}
                            </div>

                            <div className="row-span-1  min-h-[161.04px]  rounded-md  border-2 border-solid border-light_blue bg-[#FCFCFC]">
                                <Link
                                    to="/home-new"
                                    className="text-current flex justify-center text-xl items-center h-full"
                                >
                                    <span className="mr-3" style={{ color: 'blue_bg' }}>
                                        Xem thêm
                                    </span>
                                    <img src="\img\icon_arrowright_red.png" alt="" />
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="home__body-item mt-20">
                    <h1 className="text-center text-3xl font-semibold text-[#373737]">CÁC KHOA ĐÀO TẠO NỔI BẬT</h1>
                    <div className="home__body-item-line flex justify-center mt-3 m-auto">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>

                    <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 mt-12 w-[69.2708333%] m-auto gap-4 ">
                        {departmentSimple &&
                            departmentSimple.map((department, index) => {
                                return (
                                    <div
                                        key={index}
                                        className="rounded-md overflow-hidden border-2 border-solid border-light_blue"
                                    >
                                        <Link
                                            to={`home-departments/${department.id}`}
                                            className="grid grid-rows-3 h-52 text-current"
                                        >
                                            <div className="row-span-2 overflow-hidden">
                                                <img
                                                    src={
                                                        department.department_image
                                                            ? department.department_image
                                                            : 'https://img.lovepik.com/photo/40015/9423.jpg_wh860.jpg'
                                                    }
                                                    alt={department?.department_name}
                                                />
                                            </div>
                                            <div className="row-span-1 flex flex-col justify-around items-center">
                                                <p className="uppercase text-lg">{department.department_name}</p>
                                                <p className="text-slate-400">{department.courses.length} khóa học</p>
                                            </div>
                                        </Link>
                                    </div>
                                )
                            })}
                        <div className="rounded-md overflow-hidden border-2 border-solid border-light_blue">
                            <Link
                                to="home-list-training"
                                className="text-current flex justify-center text-xl items-center h-52"
                            >
                                <span className="mr-3" style={{ color: 'blue_bg' }}>
                                    Xem thêm
                                </span>
                                <img src="\img\icon_arrowright_red.png" alt="" />
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="home__body-item mt-20">
                    <h1 className="text-center text-3xl font-semibold text-[#373737]">
                        HƯỚNG DẪN SỬ DỤNG HỆ THỐNG LMS - SDU
                    </h1>
                    <div className="home__body-item-line flex justify-center mt-3 m-auto">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>

                    <div className="grid sx:grid-cols-1 sx:gap-y-10 lg:grid-cols-4 md:grid-cols-2 mt-12 gap-7 w-[69.2708333%] m-auto">
                        {ListTutorials &&
                            ListTutorials.map((tutorial, index) => {
                                return (
                                    <div
                                        onDoubleClick={(url) => viewVideo(tutorial.tutorial_video)}
                                        key={index}
                                        className="grid gap-4 lg:grid-cols-1 sx:grid-cols-1 sm:grid-cols-3  grid-cols-1 h-auto border-2 border-solid border-green grid-flow-row"
                                    >
                                        <div className="rounded-md flex sx:col-span-2 max-h-[120px] min-h-[120px] sm:col-span-1 justify-center items-center bg-slate-300">
                                            <img
                                                className="w-full h-full"
                                                src={
                                                    tutorial?.tutorial_image
                                                        ? tutorial?.tutorial_image
                                                        : 'imgplayorpause.png'
                                                }
                                                alt={tutorial?.tutorial_title ? tutorial?.tutorial_title : 'ảnh'}
                                            />
                                        </div>

                                        {/* <div className="rounded-md flex sx:col-span-2 max-h-[140px] min-h-[120px] sm:col-span-1 justify-center items-center bg-slate-300">
                                            <video>
                                                <source
                                                    className="object-cover"
                                                    src={
                                                        tutorial.tutorial_video ||
                                                        'http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerEscapes.mp4'
                                                    }
                                                    type="video/mp4"
                                                />
                                            </video>
                                        </div> */}
                                        <div className="col-span-2 rounded-md p-1">
                                            <p className="text-xl font-medium">{tutorial.tutorial_title}</p>
                                            <p className="text-slate-400 mt-1">
                                                {fomatDate(tutorial.tutorial_createdDate)}
                                            </p>
                                        </div>
                                    </div>
                                )
                            })}
                    </div>
                </div>

                <div className="flex justify-center mt-14">
                    <button onClick={handleHefp} className="btn-see_more rounded-md font-semibold bg-[#FCFCFC]">
                        <span className="mr-3" style={{ color: 'blue_bg' }}>
                            Xem thêm
                        </span>
                        <img src="\img\icon_arrowright_red.png" alt="" />
                    </button>
                </div>

                <div className="flex justify-center mt-20">
                    <div className="home__body-item-line flex justify-center mt-3 m-auto mb-7">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                </div>
                {isShowVideo && (
                    <ViewVideoComponent
                        urlVider={urlVider ? urlVider : ''}
                        setShowVideo={(ishow) => setShowVideo(ishow)}
                    />
                )}
            </div>
        </>
    )
}

export default HomeContainer
