import React, { ChangeEvent, FormEvent, useEffect } from 'react'
import { useState } from 'react'
import { StringIfPlural } from 'react-i18next'
import { useNavigate, useParams } from 'react-router-dom'
import TextEditor from '../../components/TextEditor'
import { CourseService } from '../../services/CourseService'
import { LessonService } from '../../services/LessonService'
import { UploadService } from '../../services/UploadService'
import { Course, Lesson } from '../../types'

type LessonType = {

    lesson_name: string;
    lesson_notification: string;
    lesson_content: string;
    lesson_file?:string;
    timeStart?: string;
    timeEnd?: string;
    course: {
        id: string | number
    }
}

const LessonCreateContainer: React.FC = (props) => {
    const [inputData, setInputData] = useState<Lesson>({
        lesson_name: '',
        lesson_notification: '',
        lesson_content: null,
        lesson_file: null,
        timeStart: null,
        timeEnd: null,
        course: {
            id: '',
        },
    });

  

    const [courseByLesson, setCourse] = useState<string>();

    const params = useParams()
    const navigate = useNavigate();

    const [isShowModal, setShowModal] = useState<boolean>(false);
 
    const [lessonFile, setlessonFile] = useState<File>();

   


 
    
 

  

    useEffect(() => {
        if (params.course) {
            setInputData({
                ...inputData,
                course: {
                    id: params.course,
                },
            })

            ;(async ()=> {
                const course:Course = await CourseService.getCourseById(params.course);
                setCourse(course.courseName);
            })()


        }
    }, [params.course])


    useEffect(()=> {
        if(params.lessonId) {
            ;(async ()=> {
                const lessonUpdate:any = await LessonService.getLessonById(params.lessonId);
               setInputData({
                    id: lessonUpdate.id,
                    lesson_name: lessonUpdate.lesson_name,
                    lesson_notification: lessonUpdate.lesson_notification,
                    lesson_content: lessonUpdate.lesson_timeEnd,
                    timeEnd: lessonUpdate.lesson_timeStart,
                    timeStart: lessonUpdate.timeStart,
                    course: {
                        id: lessonUpdate.course.id
                    }
                 
               })



               setCourse(lessonUpdate.course.courseName);

            })()
        }
    }, [params.lessonId])

    const handleChangeInput = (e: ChangeEvent<HTMLInputElement>) => {
        setInputData({ ...inputData, [e.target.name]: e.target.value })
    }
    const handleChangeFiles = (e: ChangeEvent<HTMLInputElement>) => {
        const { files } = e.target

        setlessonFile(files[0]);
    }

   


    const handleCancel = () => {
       navigate(-1)
    }
    const handleSubmit = async () => {

        let urlFile;
       if(lessonFile) {
        const formData = new FormData();
             formData.append("file", lessonFile);
             urlFile = await UploadService.uploadFile(formData)
             setlessonFile(null);
       }
        let data:Lesson;

        if(params.lessonId) {
            data = await LessonService.updateLesson(params.lessonId, {
                ...inputData, 
                lesson_file: urlFile ? urlFile : inputData.lesson_file
            });
        }else {
            data = await LessonService.postLessons({
                ...inputData,
                lesson_file: urlFile ? urlFile : null
            })

        }
        


        setInputData(null)
        if(data) {
            navigate(`/faculty/lesson-detail/${data.id}`);  
        }

       
    }

    const showModal = ()=> {
        setShowModal(!isShowModal);
    }

    const goBack = ()=> {
        navigate(-1);
    }

   

    return (
        <div className="w-full py-5 px-4">

        <div className="flex m-6  items-center cursor-pointer" onClick={goBack}>
                <span>
                    <img src="\img\goback.png" />
                </span>
                <span className="ml-2 uppercase text-xl text-[#636363] font-bold">Quay lại</span>
            </div>
            {/* header card */}
            <div className="course-header card">
                <h4 className="text-green uppercase font-[700] text-[40px] leading-[48px] mb-[32px]">
                    {courseByLesson}
                </h4>
                <div className="font-[500] text-[14px] leading-[17px]">
                    Khóa học của tôi /  {courseByLesson}
                </div>
            </div>

            {/* create card */}
            <div className="course-create card">
                <div className="mb-[34px]">
                    <label htmlFor="name" className="input-title">
                        Tên bài giảng <span>*</span>
                    </label>
                    <input
                        className="text-input w-full"
                        type={'text'}
                        id="name"
                        name="lesson_name"
                        value={inputData?.lesson_name}
                        onChange={handleChangeInput}
                    />
                </div>
                <div className="mb-[34px]">
                    <label htmlFor="notification" className="input-title">
                        Thông báo chung <span>*</span>
                    </label>
                    <input
                        className="text-input w-full"
                        type={'text'}
                        id="notification"
                        name="lesson_notification"
                        onChange={handleChangeInput}
                    />
                </div>             
                    <div className="mb-[34px]">
                        <label htmlFor="content" className="input-title">
                            Nội dung bài giảng <span>*</span>
                        </label>
                        <TextEditor
                            value={inputData?.lesson_content || ""}
                            onChange={(value) =>
                                setInputData({
                                    ...inputData,
                                    lesson_content: value,
                                })
                            }
                        />
                    </div>


              
                    <div className="mb-[34px]">
                        <p className="input-title">Đính kèm bài giảng có sẵn</p>
                        <div className="card">
                            <div className="flex items-baseline mb-[22px]">
                                <input id="filesInput" type={'file'} onChange={handleChangeFiles} multiple hidden />
                                <label htmlFor="filesInput" onClick={showModal} className="flex items-center justify-center btn mr-[12px] ">
                                    <img className="mr-[12px]" src="/img/Upload.png" width={'22%'} />
                                    <span className="text-[18px] leading-[22px] font-[500]">Tải lên</span>
                                </label>
                                <span className="text-[14px] leading-[17px]">Tải lên bài giảng, video bài giảng.</span>
                            </div>
                        <div className="grid grid-cols-6 gap-[22px]">
                            {

                                lessonFile ? (
                                    <div className="card p-[10px] flex flex-col justify-between aspect-square rounded-[15px] shadow hover:shadow-lg overflow-hidden">                                                
                                                    <div className="h-4/6 flex justify-center items-center">
                                                        <img src="\img\pdf.png" width={'40%'} />
                                                </div>
                                                    <div className="h-2/6 flex items-center justify-center overflow-hidden">
                                                    <p className="text-center text-[14px] leading-[18px] ">Bài giảng</p>
                                                </div>
                                                </div>
                                ):""
                            }
                        </div>  
                        </div>
                    </div>


               
                <div className="flex justify-end mb-[34px]">
                    <button className="btn mr-[13px] bg-[#e4e4e4] hover:shadow-md" onClick={handleCancel}>
                        Hủy bài giảng
                    </button>
                    <button className="btn bg-[#696969] text-white hover:shadow-md" onClick={handleSubmit}>
                       Lưu bài giảng
                    </button>
                </div>
              
            </div>
        </div>
    )
}

 

export default LessonCreateContainer
