import clsx from 'clsx'
import { useContext, useEffect, useState, ChangeEvent } from 'react'
import { useSearchParams } from 'react-router-dom'
import { SidebarContext } from '../../App'
import Faculty from '../../components/Faculty'
import Paging from '../../components/Paging'
import { ContextLayout } from '../../layout/Layout'
import { CourseService } from '../../services/CourseService'
import CalendarContainer from '../common/CalendarContainer'
// import SideRightContainer from '../common/SideRightContainer'
// import UserInfo from './../../components/UserInfo'
import { LecturerService } from './../../services/LecturerService';
import Loading from './../../components/Loading';

type props = {}

export default function TeacherFaculty({}: props) {
    const { AttachChidrenSideRight } = useContext(ContextLayout)
    const [courses, setCourses] = useState<any[]>([])
    const [CourseHistory, setCourseHistory] = useState<any>([]);
    const [searchText, setSearchText] = useState<string>(null);
    const [currenPage, setCurrenPage] = useState<any>(1);
    const [total, setTotal] = useState<number>(1);
    const [searchParam] = useSearchParams();
    const [isShowLoadingHistory, setIsShowLoadingHistory] = useState<boolean>(true);
    const [isShowLoadingCourse, setIsShowLoadingCourse] = useState<boolean>(true);

    const { collapsed } = useContext(SidebarContext)

    useEffect(()=>  {

        let timeout:any 
        if(searchText  !== null) {
            if(searchText !== "") {
            timeout = setTimeout(()=> {
                    getBySearchText(searchText, currenPage);
                }, 1000)
            }else {
                getCourse(currenPage);
            }
        }

        return ()=> {
            clearTimeout(timeout);
            
        }
    }, [searchText])


    useEffect(() => {
        ;(async ()=> {
           LecturerService.getCourseByLectureHistory().then((data)=> {
                setCourseHistory(data);
                setIsShowLoadingHistory(false)

            }).catch(()=> {
                setIsShowLoadingHistory(false)
            });
        })()

        AttachChidrenSideRight(<CalendarContainer />)

        return ()=> {
            setCourses([])
            setCourseHistory([]);
        }
    }, [])

    useEffect(()=> {
        const page = searchParam.get('page');
        if(page) {
            setCurrenPage(page);
        }

    }, [searchParam])

    useEffect(()=> {
        setIsShowLoadingCourse(true);

        if(searchText !== "" && searchText !== null) {
            console.log(searchText);
            getBySearchText(searchText, currenPage)
        }else {
            getCourse(currenPage);
        }
    }, [currenPage])


    const getCourse =  (page:any)=> {
        CourseService.getCourseByLecture({page: page - 1, size: 10, sort: 'id,desc'}).then((data)=> {

            setCourses(data.content)
            setTotal(data.totalElements)
            setIsShowLoadingCourse(false);          

        }).catch(()=> {
            setIsShowLoadingCourse(false);          
        })
        
    }

    const getBySearchText =  (text:string, page:any) => {
       CourseService.searchCourseByLecturer(text, {
            page: page - 1,
            size: 10,
            sort: 'id,desc'
        }).then((data)=> {
            setCourses(data.content);
            setTotal(data.totalElements)
            setIsShowLoadingCourse(false);          


        }).catch(()=> {
            setIsShowLoadingCourse(false);          
        });
    }

    const searchByKeyWord = (event:ChangeEvent<HTMLInputElement>)=> {
     
            setSearchText(event.currentTarget.value);
            setIsShowLoadingCourse(true);
      
    }

    return (
        <div
            className={clsx('w-full lg:pl-[44px]  lg:h-auto flex transition-all duration-500', {
                'md:pr-[44px] xl:pr-[206px]': collapsed,
                'md:pr-[44px] xl:pr-[106px]': !collapsed,
            })}
        >
            <div className="dashboard-content lg:w-[100%] pb-[123px]">
                <div className='flex items-center justify-between'>
                <h1 className="text-3xl font-semibold my-6">TRUY CẬP GẦN ĐÂY</h1>
               
                </div>               

                <div>
                    {
                        isShowLoadingHistory ? <div className='flex my-10 justify-center'><Loading size={36} /></div> : (
                            <div
                                className={clsx('grid  gap-4', {
                                    'grid-cols-3': collapsed,
                                    'grid-cols-4': !collapsed,
                                })}
                            >
                                {
                                    CourseHistory.map((course:any, index:any)=> {
                                        return <Faculty id={course.courseId} course={course} key={index} />
                                    })
                                }
                            </div>
                        )
                    }

                </div>

                <div>
                    <div className='flex justify-between items-center'>

                    <p className="text-3xl font-semibold my-6">DANH SÁCH KHÓA HỌC</p>
                    <div className="relative flex grow items-center max-w-[400px]  h-full group">
                    <svg
                        className="absolute left-0 z-20 hidden w-4 h-4 ml-4 text-gray-500 pointer-events-none fill-current group-hover:text-gray-400 sm:block"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 20 20"
                    >
                        <path d="M12.9 14.32a8 8 0 1 1 1.41-1.41l5.35 5.33-1.42 1.42-5.33-5.34zM8 14A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"></path>
                    </svg>
                    <input
                        type="text"
                        onChange={searchByKeyWord}
                        className="ml-auto text-xl grow w-full h-12 py-1.5 pl-10 pr-4 leading-normal rounded focus:border-transparent focus:outline-none focus:ring-2 focus:bg-gray-100 ring-opacity-90 border-solid border-2"
                        placeholder="Tìm kiếm khóa học của bạn..."
                    />
                </div>
                    </div>

                    {
                        isShowLoadingCourse ? ( <div className='flex my-10 justify-center'><Loading size={36} /></div>) : (
                            <div
                                className={clsx('grid  gap-4', {
                                    'grid-cols-3': collapsed,
                                    'grid-cols-4': !collapsed,
                                })}
                            >
                                {courses.map((course: any, index) => {
                                    return <Faculty id={course?.id} key={index} course={course} />
                                })}

                            
                            </div>
                        )
                    }
                  
                    <Paging total={Math.ceil(total / 10)} currenPage={currenPage} setCurrentPage={setCurrenPage} />
                </div>
            </div>
        </div>
    )
}

