import { ChangeEvent, useContext, useEffect, useRef, useState } from 'react'
import { CheckOutlined } from '@ant-design/icons'
import { useNavigate, useParams, useSearchParams } from 'react-router-dom'
import Paging from '../../components/Paging'
import SearchStudentModal from './SearchStudentModal'
import { UploadService } from '../../services/UploadService'
import { CourseService } from '../../services/CourseService'
import { LecturerService } from '../../services/LecturerService'
import { ContextModal } from '../../Context/ModalContext'
import { StudentService } from '../../services/StudentService'
import { ContextMessage } from '../../Context/ShowMessage'
import DialogConfirm from '../common/DialogConfirm'

type courseState = {
    id?: number | string
    courseNotification: string
    courseName: string
    courseCode: string
    courseDescription: string | null
    courseTotalStudent: number
    courseCreatedDate: string
    courseSemester: string
    courseImage: string
    lecturer: { id: number | string }
    department: { id: number | string }
}

const PAGE_SIZE = 5 //PM HieuTM set
function ManagerUpdateClassCotainer() {
    const { pushMessage } = useContext(ContextMessage)
    const navigate = useNavigate()

    const [itemsCheck, setItemCheck] = useState<any[]>([])

    const [currenPage, setCurrentPage] = useState<any | null>(1)
    const [totalPage, setTotalPage] = useState<any>(10)
    const [searchParams] = useSearchParams()
    useEffect(() => {
        setCurrentPage(searchParams.get('page') ? searchParams.get('page') : 1)
    }, [searchParams.get('page')])

    const { setModal, showModal } = useContext(ContextModal)

    const d = new Date()
    const [courser, setCourse] = useState<courseState | null>({
        id: '0',
        courseNotification: 'courseNotification',
        courseName: '',
        courseCode: '',
        courseDescription: 'courseDescription',
        courseTotalStudent: 0,
        courseCreatedDate: `${Intl.DateTimeFormat('en', { year: 'numeric' }).format(d)}-${Intl.DateTimeFormat('en', {
            month: '2-digit',
        }).format(d)}-${Intl.DateTimeFormat('en', { day: '2-digit' }).format(d)}`,
        courseSemester: '',
        courseImage: '',
        lecturer: { id: '' },
        department: { id: '' },
    })

    const [file, setFiles] = useState<File | null>()
    const [urlAvatar, setUrlAvatar] = useState<string | null>(null)

    const hanldeChangeFile = (events: ChangeEvent<HTMLInputElement>) => {
        const file: File = events.target.files![0]

        if (file.type.match('image/*')) {
            setUrlAvatar(URL.createObjectURL(file))
            setFiles(file)
        }
    }

    // xử lí input changes
    const handleChangeInput = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        event.preventDefault()
        const name = event.target.name
        const value = event.target.value
        if (name === 'lecturer') {
            setCourse({
                ...courser,
                [name]: { id: value },
            })
        } else {
            setCourse({
                ...courser,
                [name]: value,
            })
        }
    }

    const params = useParams()
    const getCourseData = async () => {
        CourseService.getCourseById(params.courseId)
            .then((res) => {
                setCourse({
                    ...courser,
                    id: res.id,
                    courseNotification: res.courseNotification,
                    courseName: res.courseName,
                    courseCode: res.courseCode,
                    courseDescription: res.courseDescription,
                    courseTotalStudent: res.courseTotalStudent,
                    courseCreatedDate: res.courseCreatedDate,
                    courseSemester: res.courseSemester,
                    courseImage: res.courseImage,
                    lecturer: { id: res.lecturer.id },
                    department: { id: res.department.id },
                })
            })
            .catch((er) => {
                console.log(er)
            })
    }

    const [listStudent, setListStudent] = useState<any[]>([])
    const getCourseStudents = async () => {
        StudentService.getStudentsByCourseId(params.courseId, currenPage - 1, PAGE_SIZE)
            .then((res) => {
                // console.log("re", res)
                setListStudent(res.content)
                setTotalPage(res.totalPages)
            })
            .catch((error) => {
                console.log(error)
            })
    }

    useEffect(() => {
        getCourseData()
        getCourseStudents()
    }, [params])

    const [lecturerList, setLecturerList] = useState([])
    const getLecturer = async () => {
        LecturerService.getLecturers()
            .then((res) => {
                console.log(res)
                setLecturerList(res.content)
            })
            .catch((err) => console.log(err))
    }

    useEffect(() => {
        getLecturer()
    }, [])

    // handle submit
    const handleSubmit = (event: React.MouseEvent<HTMLButtonElement>) => {
        event.preventDefault()
        if (!(courser.courseName && courser.courseSemester && courser.lecturer.id && courser.department.id)) {
            pushMessage({ type: 'WARN', title: 'Warning', message: 'Nhập các trường còn thiếu' })
            return
        }
        if (file) {
            const formdata = new FormData()
            formdata.append('file', file)
            UploadService.uploadFile(formdata)
                .then((image) => {
                    CourseService.putCourse(courser.id, { ...courser, courseImage: image })
                        .then(() => {
                            pushMessage({ type: 'SUCCESS', title: 'Success', message: 'Sửa khóa học thành công' })
                            getCourseData()
                            setFiles(null)
                            setUrlAvatar(null)
                        })
                        .catch((error) => {
                            pushMessage({ type: 'ERROR', title: 'Error', message: 'Sửa khóa học thất bại' })
                            console.log(error)
                        })
                })
                .catch((error) => {
                    console.log(error)
                })
        } else {
            CourseService.putCourse(courser.id, courser)
                .then(() => {
                    pushMessage({ type: 'SUCCESS', title: 'Success', message: 'Sửa khóa học thành công' })
                })
                .catch((error) => {
                    console.log(error)
                })
        }
    }

    const refCheckboxs = useRef<HTMLInputElement[] | any[]>([])
    const refCheckboxAll = useRef<HTMLInputElement | null>(null)

    const listenCheckAll = () => {
        const isCheckAll = itemsCheck.length === listStudent.length
        refCheckboxAll.current!.checked = isCheckAll
    }

    useEffect(() => {
        listenCheckAll()
    }, [itemsCheck, listStudent])

    const handleCheckAll = (e: ChangeEvent<HTMLInputElement>) => {
        const isCheckAll = e.target.checked
        refCheckboxs.current.forEach((item: HTMLInputElement, index) => {
            if (isCheckAll) {
                setItemCheck([...listStudent])
            } else {
                setItemCheck([])
            }
            item.checked = isCheckAll
        })
    }

    const handleCheckItems = (e: ChangeEvent<HTMLInputElement>, index: number) => {
        const isChecked = refCheckboxs.current[index].checked
        if (isChecked) {
            setItemCheck([...itemsCheck, listStudent[index]])
        } else {
            const tempArr = itemsCheck.filter((item) => {
                return item !== listStudent[index]
            })
            setItemCheck([...tempArr])
        }
    }

    const addStudentToClass = (event: React.FormEvent<HTMLFormElement>, arrayStudent: any[]) => {
        event.preventDefault()
        const tempArr = arrayStudent.map((value) => {
            return value.id
        })
        console.log(tempArr)
        CourseService.addStudentToCourse(params.courseId, tempArr)
            .then(() => {
                getCourseStudents()
                showModal(false)
            })
            .catch((err) => console.log(err))
    }

    const refreshCourseStudentData = () => {
        refCheckboxAll.current.checked = false
        refCheckboxs.current.forEach((item) => {
            item.checked = false
        })
        setItemCheck([])
        getCourseStudents()
    }

    const delStudentModal = () => {
        setModal(<DialogConfirm title="Xóa học viên" onClick={delStudentOfCourse} />)
        showModal(true)
    }

    const delStudentOfCourse = () => {
        const tempArr = itemsCheck.map((value) => {
            return value.id
        })
        CourseService.deleteStudentOfCourse(params.courseId, tempArr)
            .then(() => {
                refreshCourseStudentData()
                showModal(false)
            })
            .catch((err) => console.log(err))
    }
    const addStudent = () => {
        setModal(<SearchStudentModal submitFunction={addStudentToClass} />)

        showModal(true)
    }

    return (
        <>
            <div className="m-6 flex justify-start">
                <div
                    className="flex items-center cursor-pointer"
                    onClick={() => navigate(`/manager-department/${courser.department.id}`)}
                >
                    <span>
                        <img src="\img\goback.png" />
                    </span>
                    <span className="ml-2 uppercase text-xl text-[#636363] font-bold">Quay lại</span>
                </div>
            </div>
            <div className="relative m-5 lg:h-auto rounded-lg flex border-[1px]  border-solid border-[#D4D4D4] overflow-hidden">
                <div className="w-[100%] rounded-lg">
                    <div className="p-[23px] flex border-b-[1px] border-solid border-[#D4D4D4] justify-between items-center">
                        <h1 className="uppercase text-__text_primary font-bold text-xl">Khóa học</h1>
                    </div>

                    <div className="p-[23px]">
                        <div className="grid grid-cols-2 gap-x-4">
                            <div className="flex flex-col">
                                <label htmlFor="name" className="text-lg font-medium mb-2">
                                    Tên khoá học
                                </label>
                                <input
                                    name="courseName"
                                    onChange={(event) => handleChangeInput(event)}
                                    value={courser === null ? '' : courser?.courseName}
                                    type="text"
                                    id="name"
                                    className="ml-auto grow w-full h-12 py-1.5  leading-normal rounded focus:border-transparent focus:outline-none focus:ring-2 ring-opacity-90 border-solid border-2  text-black"
                                />
                            </div>

                            <div className="flex flex-col">
                                <label htmlFor="courseSemester" className="text-lg font-medium mb-2">
                                    Học kỳ
                                </label>
                                <input
                                    onChange={(event) => handleChangeInput(event)}
                                    name="courseSemester"
                                    value={courser.courseSemester}
                                    type="text"
                                    id="courseSemester"
                                    className="ml-auto grow w-full h-12 py-1.5  leading-normal rounded focus:border-transparent focus:outline-none focus:ring-2 ring-opacity-90 border-solid border-2  text-black"
                                />
                            </div>
                        </div>

                        <div className="grid grid-cols-2 gap-x-4 mt-6">
                            <div className="flex flex-col">
                                <label htmlFor="lecturer" className="text-lg font-medium mb-2">
                                    Giảng viên hướng dẫn
                                </label>
                                <select
                                    name="lecturer"
                                    className="ml-auto grow w-full h-12 py-1.5 bg-white leading-normal rounded focus:border-transparent focus:outline-none focus:ring-2 ring-opacity-90 border-solid border-2  text-black"
                                    value={courser.lecturer.id}
                                    onChange={(event) => handleChangeInput(event)}
                                >
                                    <option key="" value={''}>
                                        Chọn giảng viên
                                    </option>

                                    {lecturerList &&
                                        lecturerList.map((value, index) => {
                                            return (
                                                <option key={index} value={value.id}>
                                                    {value.lecturer_fullname}
                                                </option>
                                            )
                                        })}
                                </select>
                            </div>
                        </div>

                        <div className="grid grid-cols-2 gap-x-4 mt-6"></div>
                        <div className="grid mt-6">
                            <div className="flex flex-col">
                                <label htmlFor="file" className="text-lg font-medium mb-2">
                                    Tải lên ảnh khóa học:
                                </label>
                                <div className="grid grid-cols-2 rounded-[10px]  border-[1px]  p-3 border-solid border-[#D0D0D0]">
                                    <div className="flex flex-col">
                                        <p>Tải lên ảnh định dạng PNG, JPEG. ( Tối đa ....mb)</p>
                                        <label
                                            htmlFor="upload_file"
                                            className="my-auto rounded-lg p-3 border-[1px] border-solid w-[120px] border-[#D0D0D0] text-lg   text-[#636363] flex items-center justify-center"
                                        >
                                            <img src="\img\upload.png" alt="upload file" />{' '}
                                            <span className="ml-1">Tải lên</span>
                                            <input
                                                type="file"
                                                onChange={hanldeChangeFile}
                                                accept={'image/*'}
                                                hidden
                                                id="upload_file"
                                            />
                                        </label>
                                    </div>
                                    <div className="">
                                        <p>Ảnh khóa học:</p>

                                        <div className="w-[250px] h-[141px] flex items-center justify-center rounded-lg bg-[#E0E0E0]">
                                            <img
                                                className="max-w-full max-h-full object-contain"
                                                src={
                                                    urlAvatar
                                                        ? urlAvatar
                                                        : courser?.courseImage
                                                        ? courser?.courseImage
                                                        : ' '
                                                }
                                                alt=""
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <br className="my-[30px]" />

                        <div className="flex-1 py-5 flex justify-end items-end">
                            <button
                                type="reset"
                                className="w-[180px] h-[50px] rounded-[10px] border-[1px] border-solid border-[#636363] text-[#636363] text-lg font-medium mx-3"
                            >
                                Xóa khóa học
                            </button>
                            <button
                                onClick={(event) => handleSubmit(event)}
                                type="submit"
                                className="w-[180px] h-[50px] rounded-[10px] bg-[#636363] text-[#fff] text-lg font-medium"
                            >
                                Sửa khóa học
                            </button>
                        </div>
                    </div>

                    <>
                        <div className="px-6 pt-6 pb-3">
                            <div className="px-6 pt-6 pb-3 flex  justify-between items-center">
                                <h1 className="uppercase text-__text_primary font-bold text-xl">Danh sách sinh viên</h1>

                                <div className="flex items-center text-xl">
                                    <div className="flex items-center mr-6 cursor-pointer" onClick={addStudent}>
                                        <img src="\img\control_point_24px.png" className="mr-2 w-7" alt="" /> Thêm
                                    </div>
                                    <div className="flex items-center text-xl" />

                                    <div className="flex items-center mr-6 cursor-pointer" onClick={delStudentModal}>
                                        <img src="\img\delete.png" className="mr-2" alt="" /> Xoá
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="w-full px-6">
                            <table className="manager_table w-full text-center text-__text_primary border-t border-solid border-[#EFEFEF]">
                                <thead>
                                    <tr className="uppercase text-lg font-bold py-12">
                                        <td>STT</td>
                                        <td>Mã sinh viên</td>
                                        <td>Tên sinh viên</td>
                                        <td>
                                            <input
                                                type="checkbox"
                                                ref={refCheckboxAll}
                                                onChange={handleCheckAll}
                                                name=""
                                                hidden
                                                id="all_user"
                                            />
                                            <label htmlFor="all_user" className="checkbox-item">
                                                <CheckOutlined />
                                            </label>
                                        </td>
                                    </tr>
                                </thead>
                                <tbody>
                                    {listStudent.map((value, index) => {
                                        return (
                                            <tr key={index}>
                                                <td>{index + 1}</td>
                                                <td>{value.student_code}</td>
                                                <td>{value.student_fullname}</td>
                                                <td>
                                                    <input
                                                        type="checkbox"
                                                        name="user_item"
                                                        onChange={(event) => handleCheckItems(event, index)}
                                                        ref={(element) => {
                                                            refCheckboxs.current[index] = element
                                                        }}
                                                        hidden
                                                        id={`userItem_${index}`}
                                                    />
                                                    <label htmlFor={`userItem_${index}`} className="checkbox-item">
                                                        <CheckOutlined />
                                                    </label>
                                                </td>
                                            </tr>
                                        )
                                    })}
                                </tbody>
                            </table>

                            {<Paging currenPage={currenPage} setCurrentPage={setCurrentPage} total={totalPage} />}
                        </div>
                    </>
                </div>
            </div>
        </>
    )
}

export default ManagerUpdateClassCotainer
