import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Title from '../../components/Homecomponent/Title'
import { DepartmentService } from '../../services/DepartmentService'
import LineBreack from './LineBreack'
import BannerHome from '../../components/BannerHome'

type stateType = {
    courses: any[]
    department_name: string
    department_type: string
    department_image: string
    id: number
}[]
const ListTrainningContainer = () => {
    const [listData, setListData] = useState<stateType>([])
    const [listDataEng, setListDataEng] = useState<stateType>([])

    const getApiSetList = async (type?: number, page?: number, size?: number) => {
        const resultData = await DepartmentService.getDepartmentByType(type, page, size)
        if (resultData) {
            const { totalPages, content, pageable, totalElements, size } = resultData
            setListData(content)
        }
    }
    const getApiSetListEng = async (type?: number, pages?: number, size?: number) => {
        const resultData = await DepartmentService.getDepartmentByType(type, pages, size)
        if (resultData) {
            const { totalPages, content, pageable, totalElements, size } = resultData
            setListDataEng(content)
        }
    }
    useEffect(() => {
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth',
        })
        getApiSetList(0, 0, 16)
        getApiSetListEng(1, 0, 4)
    }, [])
    return (
        <>
            <BannerHome />
            <div className="container-or md:px-0 m-auto w-[68.75%]">
                <div className="home__body-item mt-20 w-[100%]">
                    <Title title=" DANH SÁCH CÁC CHUYÊN NGHÀNH ĐÀO TẠO CƠ BẢN" />

                    <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 grid-cols-1 mt-12  gap-4 mb-20 w-[100%]">
                        {listData &&
                            listData.map((data, index) => {
                                return (
                                    <div
                                        key={index}
                                        className="rounded-md overflow-hidden border-2 border-solid border-light_blue w-[95.85%] h-[240px]"
                                    >
                                        <Link
                                            to={`/home-departments/${data.id}`}
                                            className="text-current grid grid-rows-3 h-52"
                                        >
                                            <div className="row-span-2 overflow-hidden">
                                                <img src={data?.department_image} alt={data?.department_name} />
                                            </div>
                                            <div className="row-span-1 flex flex-col justify-around items-center">
                                                <p className="uppercase text-lg">{data?.department_name}</p>
                                                <p className="text-slate-400">{data?.courses.length} khóa học</p>
                                            </div>
                                        </Link>
                                    </div>
                                )
                            })}
                    </div>
                    <Title title="DANH SÁCH CHƯƠNG TRÌNH TIÊN TIẾN" subtitleEng="(Đào tạo bằng tiếng Anh)" />
                </div>
                <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 grid-cols-1 mt-12  gap-4 mb-20">
                    {listDataEng &&
                        listDataEng.map((data, index) => {
                            return (
                                <div
                                    key={index}
                                    className="rounded-md overflow-hidden border-2 border-solid border-light_blue w-[95.85%] h-[240px]"
                                >
                                    <Link
                                        to={`/home-departments/${data.id}`}
                                        className="text-current grid grid-rows-3 h-52"
                                    >
                                        <div className="row-span-2 overflow-hidden">
                                            <img src={data?.department_image} alt={data?.department_name} />
                                        </div>
                                        <div className="row-span-1 flex flex-col justify-around items-center">
                                            <p className="uppercase text-lg">{data?.department_name}</p>
                                            <p className="text-slate-400">{data?.courses.length} khóa học</p>
                                        </div>
                                    </Link>
                                </div>
                            )
                        })}
                </div>
            </div>
            <LineBreack />
        </>
    )
}

export default ListTrainningContainer
