import { useContext, useEffect, useState } from 'react'
import { ContextModal } from './../../Context/ModalContext'

type Props = {
    formDefaultValue?: any
    funcSubmit: (formValue: any) => void
}

const CreateNotification = (props: Props) => {
    const { formDefaultValue, funcSubmit } = props
    const { showModal } = useContext(ContextModal)
    const parseAuthorities = (authorities: any) => {
        let result
        switch (authorities) {
            case [
                {
                    name: 'ROLE_STUDENT',
                },
            ]:
                result = 'STUDENT'
                break
            case [
                {
                    name: 'ROLE_LECTURER',
                },
            ]:
                result = 'LECTURER'
                break
            default:
                result = 'ALL'
                break
        }
        return result
    }

    const [submitForm, setSubmitForm] = useState<any>({
        notificationTitle: formDefaultValue ? formDefaultValue.notificationTitle : '',
        notificationContent: formDefaultValue ? formDefaultValue.notificationContent : '',
        authorities: formDefaultValue ? parseAuthorities(formDefaultValue.authorities) : 'ALL',
    })

    const changeSubmitForm = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        setSubmitForm({ ...submitForm, [event.target.name]: event.target.value })
    }

    // useEffect(() => {
    //     console.log(submitForm)
    // }, [submitForm])

    return (
        <div className="h-[602px] p-[41px] w-[904px] bg-white rounded-3xl">
            <div className="flex flex-col justify-around h-full">
                <div className="flex flex-col">
                    <label htmlFor="notificationTitle" className="text-lg font-medium mb-2">
                        Tiêu đề:
                    </label>
                    <input
                        type="text"
                        id="notificationTitle"
                        name="notificationTitle"
                        className="grow w-full h-[60px] py-1.5 leading-normal rounded focus:border-transparent focus:outline-none focus:ring-2 ring-opacity-90 border-solid border-2  text-black"
                        onChange={(event) => changeSubmitForm(event)}
                        value={submitForm.notificationTitle}
                    />
                </div>

                <div className="flex flex-col">
                    <label htmlFor="notificationContent" className="text-lg font-medium mb-2">
                        Nội dung thông báo:
                    </label>

                    <textarea
                        className="w-full rounded py-1.5 focus:border-transparent focus:outline-none focus:ring-2 ring-opacity-90 border-solid border-2  text-black"
                        rows={5}
                        id="notificationContent"
                        name="notificationContent"
                        onChange={(event) => {
                            changeSubmitForm(event)
                        }}
                        value={submitForm.notificationContent}
                    ></textarea>
                </div>

                <div className="flex flex-col">
                    <label htmlFor="authorities" className="text-lg font-medium mb-2">
                        Đối tượng thông báo:
                    </label>
                    <select
                        id="authorities"
                        name="authorities"
                        className="text-[#636363] text-2xl h-[60px] font-normal  w-[404px] p-1.5  leading-normal rounded focus:border-transparent focus:outline-none focus:ring-2 ring-opacity-90 border-solid border-2"
                        onChange={(event) => changeSubmitForm(event)}
                        value={submitForm.authorities}
                    >
                        <option value={'ALL'}>All</option>
                        <option value={'STUDENT'}>Sinh viên</option>
                        <option value={'LECTURER'}>Giáo viên</option>
                    </select>
                </div>

                <div className="flex justify-end">
                    <button
                        className="px-6 py-[14px] rounded-[10px] border-[1px] border-solid border-[#636363] text-[#636363] text-lg font-medium mx-3"
                        onClick={() => showModal(false)}
                    >
                        Huỷ
                    </button>
                    <button
                        className="px-6 py-[14px] rounded-[10px] bg-[#636363] text-[#fff] text-lg font-medium"
                        onClick={() => funcSubmit(submitForm)}
                    >
                        Lưu thông báo
                    </button>
                </div>
            </div>
        </div>
    )
}

export default CreateNotification
