import React from 'react'
import UserService from '../services/UserService'
type Props = {
    isLoggedIn: () => boolean
    handleLogin: () => void
}

function BannerHome() {
    const { isLoggedIn, doLogin, doLogout } = UserService
    const handleLogin = () => {
        if (!isLoggedIn()) {
            doLogin()
        } else {
            doLogout()
        }
    }

    return (
        <div className="home-banner">
            <img
                className="w-full object-cover"
                alt=""
                style={{ maxHeight: 678 }}
                src="https://saodo.edu.vn/uploads/slider/33-full.jpg"
            />

            <div className="home-banner-sub">
                {/* <h1 className="home-banner-title text-[#373737]">CHÀO MỪNG BẠN ĐẾN SDU LMS</h1>
                <p className="home-banner-subtitle text-lg text-[#373737]">
                    Hệ thống quản lý nội dung học tập trực tuyến Trường đại học Nông Lâm Thái Nguyên
                </p> */}

                <div className="home-banner-group">
                    <button className="btn bg-blue_bg text-base text-white border-0" onClick={handleLogin}>
                        {isLoggedIn() ? 'Đăng xuất' : 'Đăng nhập'}
                    </button>
                    {/* <button className="btn bg-light_blue text-base mx-3 text-white">Tìm hiểu thêm khoá học</button> */}
                </div>
            </div>
        </div>
    )
}

export default BannerHome
