import React, { ChangeEvent, FormEvent, useContext, useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import { ContextLayout } from '../../layout/Layout';
import { useNavigate, useParams } from 'react-router-dom';
import TextEditor from '../../components/TextEditor';
import { UploadService } from '../../services/UploadService';
import { ExamService } from '../../services/ExamService';
import { ContextMessage } from '../../Context/ShowMessage';

type Files = {
    file: File | null
}

type Exam = {
    examName: string,
    examOpenTime: string | null,
    examCloseTime:string | null,
    examLimittedWorkingTime: string,
    questions: [
        {
            questionsName: string | null,
            questionFile: string | null    
        }
    ],
    course: {
        id: string
    },
    typeOfExams: {
        id: 1 | 2
    }
}

function CreateEssayExam() {
    const [fileUpload, setUploadFile] = useState<File | null>(null)
    const [typeExam, setTypeExam] = useState<number>(1);
    const [contentExam, setContenExam] = useState<string>("");
    const {pushMessage} = useContext(ContextMessage);

    const [urlAvatar, setUrlAvatar] = useState<string>();
    const navigate = useNavigate();

    const param = useParams();
        const [examPost, setExamPost] = useState<Exam>({
        examName: "",
        examCloseTime: null,
        examOpenTime: null,
        examLimittedWorkingTime: "",
        questions: [
            {
                questionsName: "",
                questionFile: ""
            }
        ],
        course: {
            id: param.id  
        },
        typeOfExams:{
            id: 2
        }
    })




    const onChangeFile = (files: FileList | null) => {
        setUploadFile(files![0]);
        setUrlAvatar(URL.createObjectURL(files![0]));
    };

    const  {AttachChidrenSideRight} = useContext(ContextLayout);
    useEffect(()=> {
        AttachChidrenSideRight(null);
    }, [])

    useEffect(()=> {
        if(param.idExam) {
            getExamToEdit(param.idExam);
        }
    }, [param.idExam])

    const chosseExam = (Event:ChangeEvent<HTMLSelectElement>)=> {
        setTypeExam(+Event.target.value);
    }

    const handleChangeInput = (event:ChangeEvent<HTMLInputElement>) => {
    
        setExamPost({
            ...examPost,
            [event.target.name]: event.target.value
        })
    }

    const getExamToEdit = (id:any)=> {
        ExamService.getExamById(id).then((data)=> {
            console.log(data);
        }).catch(()=> {

        })
    }
   
    const subMitForm = async (event:FormEvent<HTMLFormElement>)=> {
        event.preventDefault();
        let file = null;
        if(typeExam === 0) {
            const formData = new FormData();
            formData.append("file", fileUpload)
            file = await UploadService.uploadFile(formData);         
        }

       await ExamService.postExamByCourse({
            ...examPost, 
            questions: [
                {
                    questionsName: contentExam,
                    questionsFile: file || ""
                }
            ]
        }).then((data) => {
            pushMessage({
                title: "Thành công",
                type: "SUCCESS",
                message: `Bài thi được tạo thành công` 
            })
            navigate(`/faculty/courseView/${param.id}`);

        }).catch((error) => {
            pushMessage({
                title: "Thất bại",
                type: "ERROR",
                message: `Lỗi hệ thông` 
            })
        });

     
        

    }

    return (
        <div className="w-full p-[3%]">
            <div className="h-auto  mt-[2%] mb-[2%] py-[2%] px-[3%] border-solid border-2 border-[#D4D4D4] rounded-[10px]">
                <b className="text-[40px] text-primary_blue">- KHÓA 60 - KHỐI 2</b>
                <p className="text-[14px] font-medium">
                    Trung tâm kiểm soát / Khóa học của tôi / GIÁO DỤC CÔNG DÂN CUỐI KHÓA - KHÓA 60 - KHỐI 2
                </p>
            </div>
             <div className="rounded-lg w-full border-solid border-2 p-6 mb-24 border-gray-300 text-lg text-[#636363] font-bold">
                        <form onSubmit={subMitForm}>
                            <label className="text-lg">
                                Tên bài thi <span className="text-red-600">*</span>
                            </label>
                            <br />
                            <input
                                type="text"
                                id="quiz-name"
                                name="examName"
                                onChange={handleChangeInput}
                                className="rounded-md border-solid w-full  focus:border-transparent focus:outline-none focus:ring-2  border-2 my-2 h-12 mr-4 border-gray-300 p-2"
                            />
                            <div className="flex flex-row grow">
                                <div className="w-2/4 py-8 grow mr-10">
                                    <div className="grid grid-cols-2 gap-3">
                                    <div className='flex flex-col w-2/4 '>
                                        <label className="text-lg">
                                            Thời gian mở làm bài <span className="text-red-600">*</span>
                                        </label>
                                        <div className="flex flex-row">

                                            <input type="datetime-local"
                                             onChange={(event:ChangeEvent<HTMLInputElement>)=> {
                                                setExamPost({
                                                        ...examPost,
                                                        examOpenTime: new Date(event.target.value).toISOString()
                                                    })
                                             }}
                                            name='examOpenTime' className="rounded-md border-solid w-full  focus:border-transparent focus:outline-none focus:ring-2  border-2 my-2 h-12 mr-4 border-gray-300 p-2"  />
                                           
                                        </div>

                                    </div>
                                    <div className='flex flex-col w-2/4 '>
                                        <label className="text-lg">
                                            Thời gian đóng bài <span className="text-red-600">*</span>
                                        </label>
                                        <div className="flex flex-row">

                                            <input type="datetime-local"
                                             onChange={(event:ChangeEvent<HTMLInputElement>)=> {
                                                setExamPost({
                                                        ...examPost,
                                                        examCloseTime: new Date(event.target.value).toISOString()
                                                    })
                                             }}
                                             name='examCloseTime'
                                            className="rounded-md border-solid w-full  focus:border-transparent focus:outline-none focus:ring-2  border-2 my-2 h-12 mr-4 border-gray-300 p-2"  />
                                           
                                        </div>

                                    </div>

                                    {/* <div className="flex flex-col">
                                        <div className="">
                                                Điểm cao nhât <span className="text-red-600"> *</span>
                                        </div>
                                            <input
                                                type="number"
                                                id="examPointMax"
                                                onChange={handleChangeInput}
                                                name="examMaxPoint"
                                                className="rounded-md border-solid w-1/2  focus:border-transparent focus:outline-none focus:ring-2  border-2 my-2 h-12 mr-4 border-gray-300 p-2"
                                            />
                                           
                                    </div>

                                    <div className="flex flex-col">
                                        <div className="">
                                                Điểm qua môn <span className="text-red-600"> *</span>
                                        </div>
                                            <input
                                                type="number"
                                                id="examPointMin"
                                                onChange={handleChangeInput}
                                                name="examMinPoint"
                                                className="rounded-md border-solid w-1/2  focus:border-transparent focus:outline-none focus:ring-2  border-2 my-2 h-12 mr-4 border-gray-300 p-2"
                                            />
                                           
                                    </div> */}

                                        <div className="flex flex-col">
                                            <div className="">
                                                    Phút <span className="text-red-600"> *</span>
                                                </div>
                                                <input
                                                    type="number"
                                                    id="quiz-name"
                                                    onChange={handleChangeInput}
                                                    name="examLimittedWorkingTime"
                                                    className="rounded-md border-solid w-1/2  focus:border-transparent focus:outline-none focus:ring-2  border-2 my-2 h-12 mr-4 border-gray-300 p-2"
                                                />
                                       
                                        </div>
                                    </div>
                                    {/* 3th col*/}
                                        
                                  
                                </div>
                              
                            </div>
                           
                    <div className=" h-auto mt-[2%] mb-[2%] py-[1%] border-solid border-1 border-[#D4D4D4] rounded-[10px] ">
                    <div className="col-span-6 sm:col-span-3">
                            <label htmlFor="option_exam" className="block text-sm font-medium text-gray-700">Lựa chọn</label>
                            <select onChange={chosseExam} defaultValue={1} id="option_exam"  className="mt-1 block w-[300px] py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm">
                                <option value={1}>Tự tạo câu hỏi</option>
                                <option value={2}>Tải lên đề thi</option>
                                
                            </select>
                        </div>
                                {
                                    typeExam === 1 ? (
                                        <div className="w-[100%] h-auto mt-[1%] mb-[1%] py-[1%]">
                                        <TextEditor
                                        value={contentExam}
                                            onChange={(event:string)=> {
                                                    setContenExam(event)
                                            }}
                                        
                                        ></TextEditor>
                                    </div>
                    
                                    ): (
                                            <div className="grid mt-6">
                                            <div className="flex flex-col">
                                                <label htmlFor="file" className="text-lg font-medium mb-2">
                                                    Tải lên ảnh tin tức:
                                                </label>
                                                <div className="grid grid-cols-2 rounded-[10px]  border-[1px]  p-3 border-solid border-[#D0D0D0]">
                                                    <div className="flex flex-col">
                                                        <p>Tải lên ảnh định dạng PNG, JPEG. ( Tối đa ....mb)</p>
                                                        <label
                                                            htmlFor="upload_file"
                                                            className="my-auto rounded-lg p-3 border-[1px] border-solid w-[120px] border-[#D0D0D0] text-lg   text-[#636363] flex items-center justify-center"
                                                        >
                                                            <img src="\img\upload.png" alt="upload file" />{' '}
                                                            <span className="ml-1">Tải lên</span>
                                                            <input
                                                                type="file"
                                                                onChange={(event) => onChangeFile(event.target.files)}
                                                                accept={'image/*'}
                                                                hidden
                                                                id="upload_file"
                                                            />
                                                        </label>
                                                    </div>
                                                    <div className="">
                                                        <p>Xem trước ảnh tải lên:</p>

                                                        <div className="w-[250px] h-[141px] flex items-center justify-center rounded-lg bg-[#E0E0E0]">
                                                            <img
                                                                className="max-w-full max-h-full object-contain"
                                                                src={urlAvatar ? urlAvatar : '/img/img_link.png'}
                                                                alt=""
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            </div>
                                )
                                }
                    
                        
                        {/*Nút Tải file bài tập lên*/}
                        <div className="w-auto h-auto mt-[1%]">
                            <div className="flex justify-end mt-[2%]">
                                <button type='reset' className="px-[1.5%] py-[1%] bg-[#ffffff] border-solid border-[#a59c9c] border-2 rounded-[10px]">
                                        Hủy
                                </button>
                                <button 
                                type='submit'
                                className="px-[1.5%] py-[1%] ml-[1%] bg-[#636363] text-[#ffffff] rounded-[10px]"
                                
                                
                                >
                                Lưu bài thi
                                </button>
                            </div>
                        </div>
                    </div>
                                               
                        </form>
                    </div>
        </div>
    )
}

export default CreateEssayExam
