import { ChangeEvent, useContext, useEffect, useRef, useState } from 'react'
import { CheckOutlined } from '@ant-design/icons'
import { useSearchParams } from 'react-router-dom'
import Paging from '../../components/Paging'
import HeaderManager from '../../components/HeaderManager'
import CreateUserModal from './CreateUserModal'
import { ContextLayout } from '../../layout/Layout'
import CalendarContainer from '../common/CalendarContainer'
import { ContextModal } from './../../Context/ModalContext'
import { ManagerUserService } from '../../services/ManagerUserService'
import { LECTURER } from '../../types'

type User = {
    userId: string | number,
    userName: string,
    authorities: string[],
    login: string,
    userCode?:string,
    // numberphone
}

function ManagerUserContainer() {
    const [listUser, setListUser] = useState<User[]>([])
    const [itemsCheck, setItemCheck] = useState<any[]>([])
    const [isShowAction, setIsShowAction] = useState<boolean>(() => {
        return itemsCheck.length > 0
    })
    const { setModal, showModal } = useContext(ContextModal)
    const [currenPage, setCurrentPage] = useState<any | null>(1)
    const [total, setTotal] = useState<number>(1)

    const refCheckboxs = useRef<HTMLInputElement[] | any[]>([])
    const refCheckboxALl = useRef<HTMLInputElement | null>(null)
    const [searchText, setSearchText] = useState<string>('')

    const [searchParams] = useSearchParams()

    const { AttachChidrenSideRight } = useContext(ContextLayout)

    useEffect(() => {
        AttachChidrenSideRight(<CalendarContainer />)
    }, [])

    useEffect(() => {
        if (searchText !== '') {
            getUserBySearch(searchText, 1)
        } else {
            setCurrentPage(1)
            getUser(currenPage)
        }
    }, [currenPage])

    useEffect(() => {
        if (searchParams.get('page')) {
            const page = searchParams.get('page')

            setCurrentPage(page)
        }
    }, [searchParams])

    const getUser = async (page: any) => {
        const data = await ManagerUserService.getUserSystem({
            page: page - 1,
            size: 10,
        })
        setListUser(data.UserRole)
        setTotal(data.totalPage)
    }

    useEffect(() => {
        if (itemsCheck) {
            listensCheckAllUse()
            setIsShowAction(itemsCheck.length > 0)
        }
    }, [itemsCheck])

    useEffect(() => {
        let timeout: any
        if (searchText !== '') {
            timeout = setTimeout(() => {
                getUserBySearch(searchText, 1)
            }, 1000)
        }

        return () => {
            clearTimeout(timeout)
        }
    }, [searchText])

    const handleCheckAll = (e: ChangeEvent<HTMLInputElement>) => {
        const isCheckAll = e.currentTarget.checked
        refCheckboxs.current.forEach((item: HTMLInputElement, index) => {
            if (isCheckAll) {
                setItemCheck(listUser.map((user) => user.userId))
            } else {
                setItemCheck([])
            }
            item.checked = isCheckAll
        })
    }

    const handleFromItemChekbox = (value: any) => {
        const newArr: User[] = listUser

        let i: number
        newArr.forEach((user, index) => {
            if (user.userId === value) {
                i = index
            }
        })

        const isChecked = refCheckboxs.current[i].checked
        if (isChecked) {
            setItemCheck([...itemsCheck, value])
        } else {
            const newsArray = itemsCheck.filter((userId) => userId !== value)
            setItemCheck([...newsArray])
        }
    }

    const listensCheckAllUse = () => {
        if (listUser.length > 0) {
            const isCheckAll = itemsCheck.length === listUser.length
            refCheckboxALl.current!.checked = isCheckAll
        }
    }

    const handleAdd = () => {
        // setModal(
        //     <CreateUserModal type="ADD" use  r={null} title={'Thêm người dùng vào hệ thống'}/>,
        // )
        // showModal(true)
    }

    const handleDelete = () => {}

    const handleWarning = () => {}

    const searchByKeyWord = (event: ChangeEvent<HTMLInputElement>) => {
        const text = event.currentTarget.value
            setSearchText(text)
            setCurrentPage(1)
       
    }

    const getUserBySearch = async (text: string, page: any) => {
        const data = await ManagerUserService.searchUserSystem(text, {
            page: page - 1,
            size: 10,
        })

        setListUser(data.UserRole)
        setTotal(data.totalPage)
    }

    const handleUpdate = () => {
      
    }

    const showDetailUser = async (idUser: any, roleName: any) => {
        const user: any = await ManagerUserService.getUserDetaill(idUser, roleName)

        setModal(
            <CreateUserModal
                user={{
                    id: user?.userId,
                    name: user?.fullname,
                    Mand: user?.userCode,
                    gender: user?.sex,
                    date: user?.birthDay,
                    number: user?.numPhone,
                    role: user?.role,
                    email: user?.email,
                }}
                title={'Chi tiết người dùng'}
                type="VIEW"
            />,
        )
        showModal(true)
    }

    return (
        <div className="relative lg:h-auto m-5 rounded-lg flex border-[1px] border-solid border-[#D4D4D4] overflow-hidden">
            <div className="lg:w-[100%] rounded-lg">
                <HeaderManager
                    searchByKeyWord={searchByKeyWord}
                    isShowAction={isShowAction}
                    title="Quản lý người dùng"
                    handleAdd={handleAdd}
                    handleDelete={handleDelete}
                    handleUpdate={handleUpdate}
                    handleWarning={handleWarning}
                    isShowWarning={false}
                />

                <div className="w-full">
                    <table className="manager_table w-full text-center text-__text_primary">
                        <thead>
                            <tr className="uppercase text-lg font-bold py-12">
                                <td>STT</td>

                                <td>Mã Người dùng</td>

                                <td>Họ và tên</td>
                                {/* <td>email</td>
                                <td>Số điện thoại</td> */}

                                <td>Vai trò</td>
                            </tr>
                        </thead>
                        <tbody>
                            {listUser.map((value, index) => {
                                return (
                                    <tr key={index}>
                                        <td>{index + 1}</td>

                                        <td>{value.userCode}</td>

                                        <td
                                            className="cursor-pointer hover:text-[#35baf6] hover:underline duration-100"
                                            onClick={() => showDetailUser(value.userCode, value.userName)}
                                        >
                                            {value.userName}
                                        </td>

                                        <td> {value.authorities}</td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </table>

                    {<Paging currenPage={currenPage} setCurrentPage={setCurrentPage} total={total} />}
                </div>
            </div>
        </div>
    )
}

export default ManagerUserContainer
