import React, { useContext, useState } from 'react';
import { LECTURER } from '../../types';
import { ContextModal } from './../../Context/ModalContext';


type Props = {
    user: any | null,
    title: string,
    type: 'ADD' | 'UPDATE' | 'VIEW',
}

function CreateUserModal({ user, title, type}:Props) {
    const [data, setData] = useState<any>(user);
    const {showModal} = useContext(ContextModal);
    return (
        <div className='w-[854px] h-[930px] flex flex-col p-[44px] bg-white rounded-lg'>  
            <h2 className='uppercase text-center text-[28px] text-[#636363] font-bold'>{title}</h2>
            <div className='mt-8 flex-1'>
                <form className='h-full flex flex-col'>
                <div className='grid grid-cols-2 gap-3'>
                        <div className='flex flex-col mb-6'>
                            <label htmlFor='' className='mb-3 text-[#636363] text-2xl font-medium'>Họ và tên</label>
                            {
                                type === "VIEW" ? (
                                    <p className='text-base font-bold'>{data?.name}</p>
                                ) : (
                                    <input readOnly value={data?.name} required className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'/>
                                )
                            }
                        </div>

                        <div className='flex flex-col mb-6'>
                            <label htmlFor='' className='mb-3 text-[#636363] text-2xl font-medium'>Mã người dùng:</label>
                            {
                                type === "VIEW" ? (
                                    <p className='text-base font-bold'>{data?.Mand}</p> 
                                ) :  (

                                    <input value={data?.Mand} required className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'/>
                                )
                            }
                        </div>


                    </div>
                        <div className='grid grid-cols-2 gap-3'>
                        
                            <div className='flex flex-col mb-6'>
                                <label htmlFor='' className='mb-3 text-[#636363] text-2xl font-medium'>Giới tính:</label>

                                {
                                    type === "VIEW" ? (
                                        <p className='text-base font-bold'>{data.gender === 0 ? "Nam" : "Nữ"}</p>

                                    ) : (
                                <select  defaultValue={data?.Mand} required className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'>
                                        <option value='male'>Nam</option>
                                        <option value='female'>Nữ</option>
                                        <option value='k'>Khác</option>
                                </select>
                                    )
                                }
                                {/* <input className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'/> */}
                            </div>

                            <div className='flex flex-col mb-6'>
                                <label htmlFor='' className='mb-3 text-[#636363] text-2xl font-medium'>Ngày sinh:</label>
                                {
                                type === "VIEW" ? (
                                    <p className='text-base font-bold'>{data?.date}</p> 
                                ) :  (

                                    <input readOnly value={data?.date}  data-date-format="YYYY MMMM DD" required type="date" className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'/>                               
                                )
                             }
                              
                            </div>
                        
                        </div>

                        <div className='grid grid-cols-2 gap-3'>
                        
                            <div className='flex flex-col mb-6'>
                                <label htmlFor='' className='mb-3 text-[#636363] text-2xl font-medium'>Số điện thoại:</label>

                                {
                                type === "VIEW" ? (
                                    <p className='text-base font-bold'>{data?.number}</p> 
                                ) :  (

                                    <input  value={data?.number} required type='text' className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'/>
                                )
                             }

                                
                              
                            </div>

                            <div className='flex flex-col mb-6'>
                                <label htmlFor='' className='mb-3 text-[#636363] text-2xl font-medium'>Phân quyền:</label>
                                {
                                type === "VIEW" ? (
                                    <p className='text-base font-bold'>{data?.role.includes(LECTURER) ? "Giảng viên" : "Sinh viên"}</p> 
                                ) :  (

                                    <select  value={data?.role} required className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'>
                                        <option value='student'>Sinh viên</option>
                                        <option value='teacher'>Giáo viên</option>
                                </select>
                                )
                             }
                               
                            </div>
                    
                        </div>

                        <div className='flex flex-col mb-6'>
                            <label htmlFor='' className='mb-3 text-[#636363] text-2xl font-medium'>Email:</label>

                            {
                                type === "VIEW" ? (
                                    <p className='text-base font-bold'>{data?.email}</p> 
                                ) :  (

                                    <input readOnly value={data?.email} required type='email' className='rounded-lg border-2 border-solid border-[#D0D0D0] w-full px-1 py-2 focus:outline-1 focus:outline-[#35baf6]'/>
                                )
                             }
                             
                           
                        </div>

                        <div className='flex-1 flex justify-end items-end'>  

                                    <button  type='reset' onClick={()=>showModal(false)} className='w-[180px] h-[50px] rounded-[10px] border-[1px] border-solid border-[#636363] text-[#636363] text-lg font-medium mx-3'>{type !== 'VIEW' ? 'Huỷ' : 'Quay lại'}</button> 
                                   {type !== 'VIEW' ? <button type='submit' className='w-[180px] h-[50px] rounded-[10px] bg-[#636363] text-[#fff] text-lg font-medium'>Cập nhật</button> : ""} 
                        </div>
                </form>
            </div>
        
        </div>
    );
}

export default CreateUserModal;
