import { AddCircle, Setting2 } from 'iconsax-react'
import React, { ChangeEvent } from 'react'

type Props = {
    title: string
    isShowAction?: boolean
    isShowWarning?: boolean
    handleAdd?: () => void
    handleDelete?: (event: React.MouseEvent<HTMLImageElement>) => void
    handleWarning?: () => void
    handleUpdate?: () => void
    searchKeyWord?: string
    searchByKeyWord?: (event: ChangeEvent<HTMLInputElement>) => void
}

function HeaderManager(props: Props) {
    const {
        isShowAction,
        handleUpdate,
        title,
        isShowWarning,
        handleAdd,
        handleDelete,
        searchKeyWord,
        searchByKeyWord,
    } = props
    return (
        <div className="p-4 flex border-[1px] border-solid border-[#D4D4D4] justify-between items-center">
            {!isShowAction ? (
                <h1 className="uppercase text-__text_primary font-bold text-xl">{title}</h1>
            ) : (
                <div className="flex items-center text-xl">
                    {/* <div className="flex items-center mr-6 cursor-pointer" onClick={handleUpdate}>
                        <img src="\img\edit.png" className="mr-2" alt="" /> Sửa
                    </div> */}

                    <div className="flex items-center mr-6 cursor-pointer" onClick={handleDelete}>
                        <img src="\img\delete.png" className="mr-2" alt="" /> Xoá
                    </div>
                   
                </div>
            )}

            {/* search */}
            <div className="flex items-center justify-between">
                <div className="relative flex grow items-center w-full lg:w-[377px] h-full group">
                    <svg
                        className="absolute left-0 z-20 hidden w-4 h-4 ml-4 text-gray-500 pointer-events-none fill-current group-hover:text-gray-400 sm:block"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 20 20"
                    >
                        <path d="M12.9 14.32a8 8 0 1 1 1.41-1.41l5.35 5.33-1.42 1.42-5.33-5.34zM8 14A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"></path>
                    </svg>
                    <input
                        type="text"
                        value={searchKeyWord}
                        onChange={searchByKeyWord}
                        className="ml-auto grow w-full h-12 py-1.5 pl-10 pr-4 leading-normal rounded focus:border-transparent focus:outline-none focus:ring-2 focus:bg-gray-100 ring-opacity-90 border-solid border-2"
                        // placeholder="Search"
                    />
                </div>

                <div className="flex items-center ml-3 p-2 rounded-md bg-btn_bg cursor-pointer" onClick={handleAdd}>
                    <span>
                        <AddCircle size="24" />
                    </span>
                    <span className="text-lg ml-1">Thêm</span>
                </div>

                {/* <span className="ml-3 text-__text_primary">
                    <Setting2 size="28" />
                </span> */}
            </div>
        </div>
    )
}

export default HeaderManager
