import { CheckOutlined } from '@ant-design/icons'
import React, { useEffect, useRef, useState } from 'react'
import { useParams } from 'react-router-dom'
import { CourseService } from '../../services/CourseService'
import { ExamService } from '../../services/ExamService'
import { __Date } from '../../types'

type Props = {}

type Answer = {
    answersName: string
    answersStatus: '1' | '0'
}
type Question = {
    questionsName: string
    answers: Answer[]
}

const CreateQuizContainer = (props: Props) => {
    const params = useParams()
    const { courseId, quizId } = params
    const [courseData, setCourseData] = useState(null)

    const getCourseData = () => {
        CourseService.getCourseById(courseId)
            .then((data) => setCourseData(data))
            .catch((err) => console.log(err))
    }

    const [examData, setExamData] = useState({
        id: quizId,
        course: {
            id: courseId,
        },
        examName: '',
        examCloseTime: '',
        examLimittedWorkingTime: '',
        examOpenTime: '',
        questions: [],
        examsHistoryAnswer: '',
        examsHistoryFileAnswer: '',
        typeOfExams: {
            id: 1,
        },
    })

    const getExamData = () => {
        ExamService.getExamById(quizId)
            .then((data) => {
                setExamData({
                    ...data,
                    examOpenTime: data.examOpenTime.slice(0, 16),
                    examCloseTime: data.examCloseTime.slice(0, 16),
                })
                setListQuestion(
                    data.questions?.map((item: Question) => {
                        return {
                            questionsName: item.questionsName,
                            answers: item.answers.map((element) => {
                                return { answersName: element.answersName, answersStatus: element.answersStatus }
                            }),
                        }
                    }),
                )
            })
            .catch((err) => console.log(err))
    }

    useEffect(() => {
        getCourseData()
        getExamData()
    }, [])

    useEffect(() => {
        console.log(examData)
    }, [examData])

    const handleChangeExamData = (event: React.ChangeEvent<HTMLInputElement>) => {
        const regex = /^[0-9\b]+$/
        if (event.target.name == 'examLimittedWorkingTime') {
            setExamData({
                ...examData,
                [event.target.name]:
                    event.target.value == '' || regex.test(event.target.value)
                        ? event.target.value
                        : examData.examLimittedWorkingTime,
            })
        } else {
            setExamData({ ...examData, [event.target.name]: event.target.value })
        }
    }

    const [listQuestion, setListQuestion] = useState<Question[]>([
        {
            questionsName: '',
            answers: [
                {
                    answersName: '',
                    answersStatus: '0',
                },
                {
                    answersName: '',
                    answersStatus: '0',
                },
                {
                    answersName: '',
                    answersStatus: '0',
                },
                {
                    answersName: '',
                    answersStatus: '0',
                },
            ],
        },
    ])
    const [chosenQuestionList, setChosenQuestionList] = useState<number[]>([])

    const refCheckboxs = useRef<HTMLInputElement[] | any[]>([])

    useEffect(() => console.log(listQuestion), [listQuestion])
    useEffect(() => console.log(chosenQuestionList), [chosenQuestionList])

    const addQuestion = (event: React.MouseEvent<HTMLButtonElement>) => {
        event.preventDefault()
        setListQuestion([
            ...listQuestion,
            {
                questionsName: '',
                answers: [
                    {
                        answersName: '',
                        answersStatus: '0',
                    },
                    {
                        answersName: '',
                        answersStatus: '0',
                    },
                    {
                        answersName: '',
                        answersStatus: '0',
                    },
                    {
                        answersName: '',
                        answersStatus: '0',
                    },
                ],
            },
        ])
    }

    const handleChangeQuestion = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>, index: number) => {
        let tempArr = listQuestion
        tempArr[index] = { ...tempArr[index], [event.target.name]: event.target.value }
        setListQuestion([...tempArr])
    }

    const handleChangeAnswer = (
        event: React.ChangeEvent<HTMLInputElement>,
        questionIndex: number,
        answerIndex: number,
    ) => {
        let tempArr = listQuestion
        tempArr[questionIndex].answers[answerIndex] = {
            ...tempArr[questionIndex].answers[answerIndex],
            answersName: event.target.value,
        }
        setListQuestion([...tempArr])
    }

    const handleChooseAnswer = (
        event: React.ChangeEvent<HTMLInputElement>,
        questionIndex: number,
        answerIndex: number,
    ) => {
        let tempArr = listQuestion

        for (let index in tempArr[questionIndex].answers) {
            tempArr[questionIndex].answers[index] = {
                ...tempArr[questionIndex].answers[index],
                answersStatus: index == answerIndex.toString() ? '1' : '0',
            }
        }
        setListQuestion([...tempArr])
    }

    const delQuestion = (event: React.MouseEvent<HTMLButtonElement>) => {
        event.preventDefault()
        const delArr = chosenQuestionList.sort((a, b) => b - a)
        let tempQuestionlist = [...listQuestion]
        delArr.forEach((item) => {
            tempQuestionlist = [
                ...tempQuestionlist.slice(0, item),
                ...tempQuestionlist.slice(item + 1, tempQuestionlist.length),
            ]
        })
        resetRef()
        setListQuestion([...tempQuestionlist])
        setChosenQuestionList([])
    }

    const resetRef = () => {
        refCheckboxs.current.forEach((item) => {
            if (item) item.checked = false
        })
    }

    const chooseAllQuestion = (event: React.MouseEvent<HTMLButtonElement>) => {
        event.preventDefault()
        const isCheckAll = listQuestion.length !== chosenQuestionList.length
        if (isCheckAll) {
            setChosenQuestionList(
                listQuestion.map((item, index) => {
                    return index
                }),
            )
        } else {
            setChosenQuestionList([])
        }
        refCheckboxs.current.forEach((item) => {
            item.checked = isCheckAll
        })
    }

    const handleCheckItems = (event: React.ChangeEvent<HTMLInputElement>, index: number) => {
        const isChecked = refCheckboxs.current[index].checked
        if (isChecked) {
            setChosenQuestionList([...chosenQuestionList, index])
        } else {
            setChosenQuestionList(chosenQuestionList.filter((item) => item != index))
        }
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault()
        if (
            !examData.examName ||
            !examData.examOpenTime ||
            !examData.examCloseTime ||
            !examData.examLimittedWorkingTime
        ) {
            console.log('Please enter empty field')
            return
        }
        ExamService.putMultipleChoiceExam(quizId, { ...examData, questions: listQuestion })
            .then(() => {
                console.log("Put Success")
                getExamData()
            })
            .catch((err) => console.log(err))
    }

    return (
        <>
            <div className="flex-row flex grow">
                <div className="quiz flex-1 flex-col p-[23px]">
                    <div>
                        <button className="border-solid border-2 border-gray-300 p-2">Quay lại</button>
                    </div>
                    <div className="border-solid border-2 border-gray-300 rounded-lg p-[20px] mt-[23px]">
                        <h1 className="uppercase font-[700] text-[40px] text-primary_blue leading-[48px] will-change-contents">
                            {courseData?.courseName}
                        </h1>
                        <p className="text-[#636363] font-[500] text-[14px] leading-[17px] mt-[32px]">
                            <a className="text-inherit hover:underline" href="/teacher-central">
                                Trung tâm kiểm soát
                            </a>{' '}
                            / <a className="text-inherit hover:underline">Khóa học của tôi</a> /{' '}
                            <a className="text-inherit hover:underline" href="/">
                                {courseData?.courseName}
                            </a>
                        </p>
                    </div>

                    <div className="mt-[43px]">
                        <h1 className="text-[24px] leading-[29px] text-primary_blue uppercase font-[500]">
                            Thiết lập bài thi trắc nghiệm
                        </h1>
                    </div>

                    <div className="rounded-lg border-solid border-2 mt-[23px] p-[23px] border-gray-300 text-[18px] leading-[21px] text-[#636363]">
                        <form
                            onSubmit={(event) => {
                                handleSubmit(event)
                            }}
                        >
                            <div>
                                <label htmlFor="quiz-name">
                                    Tên bài thi <span className="text-red-600">*</span>
                                </label>
                                <input
                                    type="text"
                                    id="examName"
                                    name="examName"
                                    className="rounded-md border-solid border-2 border-gray-300 h-[50px] w-full px-[20px] my-2"
                                    value={examData.examName}
                                    onChange={(event) => handleChangeExamData(event)}
                                />
                            </div>

                            <div className="grid grid-cols-2 gap-[26px] mt-[32px]">
                                <div className="">
                                    <label className="">
                                        Thời gian mở làm bài <span className="text-red-600">*</span>
                                    </label>
                                    <div className="flex flex-row items-baseline">
                                        <input
                                            type="datetime-local"
                                            className="rounded-md border-solid border-2 border-gray-300 h-[42px] w-full max-w-[320px] px-[9px] text-center my-2"
                                            name="examOpenTime"
                                            value={examData.examOpenTime}
                                            onChange={(event) => handleChangeExamData(event)}
                                        />
                                    </div>
                                </div>
                                <div className="">
                                    <label className="">
                                        Thời gian khóa làm bài <span className="text-red-600">*</span>
                                    </label>
                                    <div className="flex flex-row items-baseline">
                                        <input
                                            type="datetime-local"
                                            className="rounded-md border-solid border-2 border-gray-300 h-[42px] w-full max-w-[320px] px-[9px] text-center my-2"
                                            name="examCloseTime"
                                            value={examData.examCloseTime}
                                            onChange={(event) => {
                                                handleChangeExamData(event)
                                            }}
                                        />
                                    </div>
                                </div>
                                <div className="col-span-2">
                                    <label className="">
                                        Giới hạn thời gian làm bài
                                        <span className="text-red-600"> *</span>
                                    </label>
                                    <div className="flex flex-row items-baseline">
                                        <input
                                            type="text"
                                            id="examLimittedWorkingTime"
                                            className="rounded-md border-solid border-2 border-gray-300 h-[42px] w-[80px] text-center my-2"
                                            name="examLimittedWorkingTime"
                                            value={examData.examLimittedWorkingTime}
                                            onChange={(event) => handleChangeExamData(event)}
                                            placeholder="0"
                                        />
                                        <div className="px-3">
                                            Phút <span className="text-red-600"> *</span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="mt-[42px]">
                                <h2>Tạo mới bài thi trắc nghiệm</h2>
                            </div>

                            <div className="flex flex-row">
                                <button
                                    onClick={chooseAllQuestion}
                                    className="rounded-lg border-solid border-2 my-2 h-[42px] px-4 border-gray-300 p-[9px]"
                                >
                                    Chọn tất cả
                                </button>
                                <button
                                    onClick={delQuestion}
                                    className="rounded-lg border-solid border-2 my-2 h-[42px] px-4 ml-[23px] border-gray-300 p-[9px]"
                                >
                                    Xóa
                                </button>
                            </div>

                            {listQuestion.map((item, index) => {
                                return (
                                    <div key={index} className="pt-12 grid grid-cols-12 items-center">
                                        <textarea
                                            placeholder="Nhập câu hỏi..."
                                            name="questionsName"
                                            className="col-span-11 h-24 my-3 grow border-2 border-solid border-[#D4D4D4] py-2 px-6 rounded-lg flex flex-row"
                                            onChange={(event) => handleChangeQuestion(event, index)}
                                            value={item.questionsName}
                                        />

                                        <div className="col-span-1">
                                            <input
                                                type="checkbox"
                                                name="user_item"
                                                onChange={(event) => handleCheckItems(event, index)}
                                                ref={(element) => {
                                                    refCheckboxs.current[index] = element
                                                }}
                                                hidden
                                                id={`questionItem_${index}`}
                                            />
                                            <label htmlFor={`questionItem_${index}`} className="checkbox-item">
                                                <CheckOutlined />
                                            </label>
                                        </div>
                                        {[0, 1, 2, 3].map((value, idx) => {
                                            return (
                                                <>
                                                    <input
                                                        placeholder={`Đáp án ${idx + 1}`}
                                                        name={`answer_${idx}`}
                                                        className="col-span-11 h-10 my-1 grow border-2 border-solid border-[#D4D4D4] py-2 px-6 rounded-lg flex flex-row"
                                                        value={item.answers[idx].answersName}
                                                        onChange={(event) => handleChangeAnswer(event, index, idx)}
                                                    />
                                                    <input
                                                        type="radio"
                                                        id={`Answer_${index + '_' + idx}`}
                                                        name={`trueAnswer_${index}`}
                                                        onChange={(event) => {
                                                            handleChooseAnswer(event, index, idx)
                                                        }}
                                                        checked={item.answers[idx].answersStatus == '1'}
                                                    />
                                                </>
                                            )
                                        })}
                                    </div>
                                )
                            })}

                            <div className="flex justify-end mt-10 mb-2">
                                <button
                                    className="rounded-lg border-solid border-2 my-2 h-[42px] px-4 p-2 m-2"
                                    onClick={(event) => addQuestion(event)}
                                >
                                    Thêm câu hỏi
                                </button>
                            </div>

                            <div className="flex justify-end mt-10 mb-2">
                                <button className="rounded-lg border-solid border-2 my-2 h-12 px-4 m-2 border-gray-300 p-2 bg-[#6363631a]">
                                    Huỷ bài thi
                                </button>
                                <button
                                    className="rounded-lg border-solid border-2 my-2 h-12 px-4 p-2 m-2 text-[#fff] bg-[#636363]"
                                    type="submit"
                                >
                                    Upload bài thi
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateQuizContainer
