import React, { ChangeEvent, useContext, useEffect, useState } from "react"
import { ContextProfile } from "../../components/UserInfo";


type UserInfo = {
    type:  string,
    title: string,
    value: string,

}

export default function UserInfoItem({type, value, title}:UserInfo) {

   

    return <div className="w-full flex flex-col items-center">
            <div className='flex items-center justify-center'><b>{title}</b></div> 
          
                   { type !== 'gender' ?  (   <p>{value || ""}</p> ) : (<p>{+value === 0? "Nam" : "Nữ"}</p>)}
                   
             </div>
       
}   